import { Action } from '@ngrx/store';
import { HunitParams } from '../models/hunit.params';
import { HunitResponse } from '../models/hunit.response';
import { Hunit } from '../models/hunit';



export enum HunitActionType {
  Loading = '[Hunit] Loading',
  LoadSuccess = '[Hunit] LoadSuccess',
  LoadFailure = '[Hunit] LoadFailure',

  InsertSuccess = '[Hunit] InsertSuccess',
  InsertLoading = '[Hunit] InsertLoading',
  InsertError = '[Hunit] InsertError',
  Insert = '[Hunit] InsertHunit',

  UpdateSuccess = '[Hunit] UpdateSuccess',
  UpdateLoading = '[Hunit] UpdateLoading',
  UpdateError = '[Hunit] UpdateError',
  Update = '[Hunit] UpdateHunit',

  DeleteSuccess = '[Hunit] DeleteSuccess',
  DeleteLoading = '[Hunit] DeleteLoading',
  DeleteError = '[Hunit] DeleteError',
  Delete = '[Hunit] DeleteHunit',

}

export class HunitLoadAction implements Action {
  public readonly type = HunitActionType.Loading;
  constructor(public payload: HunitParams) {
  }
}

export class HunitInsertAction implements Action {
  public readonly type = HunitActionType.Insert;
  constructor(public payload: HunitParams) {}
}

export class HunitInsertLoadingAction implements Action {
  public readonly type = HunitActionType.InsertLoading;
  constructor(public payload: HunitParams) {}
}

export class HunitInsertSuccessAction implements Action {
  public readonly  type = HunitActionType.InsertSuccess;
  constructor(public payload: HunitResponse){}
 }

export class HunitInsertErrorAction implements Action{
  public readonly type = HunitActionType.InsertError;
  constructor(public error: any){}
}

export class HunitLoadSuccessAction implements Action {
  public readonly type = HunitActionType.LoadSuccess;
  constructor(public payload: HunitResponse) {

  }
}


export class HunitLoadFailAction implements Action {
  public readonly type = HunitActionType.LoadFailure;
  constructor(public error: any) {
  }
}

// update
export class HunitUpdateAction implements Action {
  public readonly type = HunitActionType.Update;
  constructor(public payload: HunitParams) {}
}



export class HunitUpdateLoadingAction implements Action {
  public readonly type = HunitActionType.UpdateLoading;
  constructor(public payload: HunitParams) {}
}

export class HunitUpdateSuccessAction implements Action {
  public readonly  type = HunitActionType.UpdateSuccess;
  constructor(public payload: HunitResponse){}
 }

export class HunitUpdateErrorAction implements Action{
  public readonly type = HunitActionType.UpdateError;
  constructor(public error: any){}
}

// update
export class HunitDeleteAction implements Action {
  public readonly type = HunitActionType.Delete;
  constructor(public payload: HunitParams) {}
}

export class HunitDeleteLoadingAction implements Action {
  public readonly type = HunitActionType.DeleteLoading;
  constructor(public payload: HunitParams) {}
}

export class HunitDeleteSuccessAction implements Action {
  public readonly  type = HunitActionType.DeleteSuccess;
  constructor(public payload: HunitResponse){}
 }

export class HunitDeleteErrorAction implements Action{
  public readonly type = HunitActionType.DeleteError;
  constructor(public error: any){}
}




export type HunitAction = HunitLoadAction
 | HunitLoadSuccessAction
 | HunitLoadFailAction

 |HunitInsertSuccessAction
 |HunitInsertErrorAction
 |HunitInsertLoadingAction
 |HunitInsertAction

 |HunitUpdateSuccessAction
 |HunitUpdateErrorAction
 |HunitUpdateLoadingAction
 |HunitUpdateAction

 |HunitDeleteSuccessAction
 |HunitDeleteErrorAction
 |HunitDeleteLoadingAction
 |HunitDeleteAction;
