import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { PropertyActionType,
  PropertyLoadAction,
  PropertyLoadSuccessAction,
  PropertyLoadFailAction,
  PropertyInsertSuccessAction,
  PropertyInsertErrorAction,
  PropertyDeleteSuccessAction,
  PropertyDeleteErrorAction,
  PropertyInsertAction,
  PropertyFilteringSuccessAction,
  PropertyDevelopmentSuccessAction} from './property.actions';
import { PropertyService } from '../services/property.service';
import { PropertyResponse } from '../models/property.response';
import { PropertyInput} from '../models/property.input';
import { PropertyParams } from '../models/property.params';
import { Property } from '../models/property';



@Injectable()
export class PropertyEffects {
  constructor(private service: PropertyService, private actions$: Actions) { }

  @Effect()
  public loadPropertys$ = this.actions$
    .pipe(ofType<PropertyLoadAction>(PropertyActionType.Loading),
      map(action => action.payload),
      switchMap((params: PropertyParams) =>
        this.service.getPropertys(params).pipe(
          map((response: PropertyResponse) => new PropertyLoadSuccessAction(response)),
          catchError((error) => of(new PropertyLoadFailAction(error))),
        ),
      ),
    );

    @Effect()
    public createPropertys$ = this.actions$
      .pipe(ofType<PropertyInsertAction>(PropertyActionType.Insert),
        map(action => action.payload),
        switchMap((params: PropertyInput) =>
          this.service.createProperty(params).pipe(
            map((response: PropertyResponse) => new PropertyInsertSuccessAction(response)),
            catchError((error) => of(new PropertyInsertErrorAction(error))),
          ),
        ),
      );

      @Effect()
      public filterPropertys$ = this.actions$
        .pipe(
          ofType<PropertyInsertAction>(PropertyActionType.Filtering),
          map(action => action.payload),
          switchMap((params: any) =>
            this.service.filterProperty(params).pipe(
              map((response: PropertyResponse) => new PropertyFilteringSuccessAction(response)),
              catchError((error) => of(new PropertyLoadFailAction(error))),
            ),
          ),
        );

        @Effect()
        public featuredPropertys$ = this.actions$
          .pipe(
            ofType<PropertyInsertAction>(PropertyActionType.Development),
            map(action => action.payload),
            switchMap((params: any) =>
              this.service.filterProperty(params).pipe(
                map((response: PropertyResponse) => new PropertyDevelopmentSuccessAction(response)),
                catchError((error) => of(new PropertyLoadFailAction(error))),
              ),
            ),
          );

        @Effect()
        public developmentPropertys$ = this.actions$
          .pipe(
            ofType<PropertyInsertAction>(PropertyActionType.Filtering),
            map(action => action.payload),
            switchMap((params: any) =>
              this.service.filterProperty(params).pipe(
                map((response: PropertyResponse) => new PropertyFilteringSuccessAction(response)),
                catchError((error) => of(new PropertyLoadFailAction(error))),
              ),
            ),
          );


      @Effect()
      public updateProperty$ = this.actions$
        .pipe(ofType<PropertyLoadAction>(PropertyActionType.Update),
          map(action => action.payload),
          switchMap((params: PropertyInput) =>
            this.service.updateProperty(params).pipe(
              map((response: PropertyResponse) => new PropertyInsertSuccessAction(response)),
              catchError((error) => of(new PropertyInsertErrorAction(error))),
            ),
          ),
        );

        @Effect()
        public deleteAmins$ = this.actions$
          .pipe(ofType<PropertyLoadAction>(PropertyActionType.Delete),
            map(action => action.payload),
            switchMap((property: Property) =>
              this.service.deleteProperty(property).pipe(
                map((response: PropertyResponse) => new PropertyDeleteSuccessAction(response)),
                catchError((error) => of(new PropertyDeleteErrorAction(error))),
              ),
            ),
          );
}
