import { Component, OnInit, ViewChild, Inject, NgZone, ElementRef } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Publication } from '../models/publication';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { Store, select } from '@ngrx/store';
import { PublicationLoadAction, PublicationDeleteAction, PublicationInsertAction, PublicationUpdateAction } from '../store/publication.actions';
import { PublicationService } from '../services/publication.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PublicationParams } from '../models/publication.params';
import { selectAllPublication, selectPublicationTotal } from '../store/publication.selectors';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppService } from '../../../app.service';
import { GlobalState } from '../../../store/states/global.state';
import { MapsAPILoader } from '@agm/core';
import { PopupComponent } from '../../popup/popup.component';


@Component({
  selector: 'app-publication',
  templateUrl: './publication.component.html',
  styleUrls: ['./publication.component.scss']
})
export class PublicationComponent implements OnInit {
  displayedColumns: string[] = ['title', 'published',  'actions' ];
  dataSource: MatTableDataSource<Publication>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public publication: Publication = {};
  private userId: string;


  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private publicationService: PublicationService,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.store.pipe(select(selectAllPublication)).subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });


    this.store.pipe(select(selectPublicationTotal)).subscribe(total => {
        this.total = total;
       });

    this.loadPublication({page: 1, limit: 10});
    this.activatedRoute.params.subscribe(params => {
      this.userId = params.id;

    });

  }

 public openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
       duration: 2000,
    }).afterDismissed().subscribe(info => {
      if (info.dismissedByAction === true) {

      }
    });
 }

public loadPublication(data){
   this.store.dispatch(new PublicationLoadAction(data));
  }

  public remove(publication: Publication) {
    this.snackBar.open('Are you sure you want to delete?', 'DELETE', {
      duration: 2000,
   }).afterDismissed().subscribe(info => {
     if (info.dismissedByAction === true) {
      this.store.dispatch(new PublicationDeleteAction(publication));
      const index: number = this.dataSource.data.indexOf(publication);
      if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Publication>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
     }});


  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

openEditDialog(publication: Publication): void {

  const dialogRef = this.dialog.open(AddPublicationComponent, {
    width: '500px',
    data: publication
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    console.log(result);

    this.publication = result;
    this.publication._id = publication._id;
    this.publication.userId = this.userId;
    const publicationParams =  { publication: this.publication} as PublicationParams;
    this.store.dispatch(new PublicationUpdateAction(publicationParams));

  });
}

openDetails(feedback: Publication){
  this.openDetailDialog(feedback);
}

openDetailDialog(detail: Publication): void {

  const dialogRef = this.dialog.open(PopupComponent, {
    width: '500px',
    data: detail
  });

  dialogRef.afterClosed().subscribe(result => {


  });
}

openDialog(publication): void {
  const dialogRef = this.dialog.open(AddPublicationComponent, {
    width: '500px',
    data: publication
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    console.log(result.file);
    if (result){
      this.publication = result;

      if (result.file && result.file[0]){
        if (result.file[0].file){
          this.publication.file = result.file[0].file;
        }else if (result.file[0].link){
          this.publication.file = result.file[0].link;
        }

      }

      const publicationParams =  { publication: this.publication} as PublicationParams;
      this.store.dispatch(new PublicationInsertAction(publicationParams));
    }

  });
}

}

@Component({
  selector: 'app-add-publication',
  templateUrl: './add-publication.component.html',
  styleUrls: ['./add-publication.component.scss']
})
export class AddPublicationComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<AddPublicationComponent>,
    public formBuilder: FormBuilder,
    public store: Store<GlobalState>,
    private appService: AppService,
    @Inject(MAT_DIALOG_DATA)
    public data: Publication) {

    }

    public title = 'Add Blog Post';
    public infoForm: FormGroup;

  public closeDialog(){
    this.dialogRef.close();
  }

  public onInfoFormSubmit(publication: Publication){
    if (this.infoForm.valid){
      this.data = publication;
      this.dialogRef.close();
    }
  }

  ngOnInit(): void {

    this.infoForm = this.formBuilder.group({
      content: ['', Validators.required],
      file:  null,
      title: ['', Validators.compose([Validators.required, Validators.minLength(3)])],

    });

    if (this.data && this.data._id){
      this.title = 'Edit Blog Post ' +   this.data.title;
      this.infoForm.patchValue(this.data);
    }


  }

}

