import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { BillActionType,
  BillLoadAction,
  BillLoadSuccessAction,
  BillLoadFailAction,
  BillInsertSuccessAction,
  BillInsertErrorAction,
  BillDeleteSuccessAction,
  BillDeleteErrorAction,
  BillInsertAction} from './bill.actions';
import { BillService } from '../services/bill.service';
import { BillResponse } from '../models/bill.response';
import { BillParams } from '../models/bill.params';
import { Bill } from '../models/bill';



@Injectable()
export class BillEffects {
  constructor(private service: BillService, private actions$: Actions) { }

  @Effect()
  public loadBills$ = this.actions$
    .pipe(ofType<BillLoadAction>(BillActionType.Loading),
      map(action => action.payload),
      switchMap((params: BillParams) =>
        this.service.getBills(params).pipe(
          map((response: BillResponse) => new BillLoadSuccessAction(response)),
          catchError((error) => of(new BillLoadFailAction(error))),
        ),
      ),
    );

    @Effect()
    public createBills$ = this.actions$
      .pipe(ofType<BillInsertAction>(BillActionType.Insert),
        map(action => action.payload),
        switchMap((params: BillParams) =>
          this.service.createBill(params).pipe(
            map((response: BillResponse) => new BillInsertSuccessAction(response)),
            catchError((error) => of(new BillInsertErrorAction(error))),
          ),
        ),
      );

      @Effect()
      public updateBill$ = this.actions$
        .pipe(ofType<BillLoadAction>(BillActionType.Update),
          map(action => action.payload),
          switchMap((params: BillParams) =>
            this.service.updateBill(params).pipe(
              map((response: BillResponse) => new BillInsertSuccessAction(response)),
              catchError((error) => of(new BillInsertErrorAction(error))),
            ),
          ),
        );

        @Effect()
        public deleteAmins$ = this.actions$
          .pipe(ofType<BillLoadAction>(BillActionType.Delete),
            map(action => action.payload),
            switchMap((bill: Bill) =>
              this.service.deleteBill(bill).pipe(
                map((response: BillResponse) => new BillDeleteSuccessAction(response)),
                catchError((error) => of(new BillDeleteErrorAction(error))),
              ),
            ),
          );
}
