
import { PropertyAction, PropertyActionType } from '../property.actions';
import { initialPropertySearchState, PropertySearchState, propertyAdapter } from './property.search.state';



export function propertySearchReducer(state = initialPropertySearchState, action: PropertyAction): PropertySearchState {
  switch (action.type) {
    case PropertyActionType.Loading: {
      return { ...state, loading: true };
    }

    case PropertyActionType.LoadSuccess: {

      return propertyAdapter.setAll(action.payload.propertys.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.propertys.page,
        total: action.payload.propertys.totalDocs,
      });
    }
    case PropertyActionType.FilteringSuccess: {

return propertyAdapter.setAll(action.payload.propertys.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.propertys.page,
        total: action.payload.propertys.totalDocs,
      });
    }
    case PropertyActionType.DeleteLoading:
    case PropertyActionType.UpdateLoading:
    case PropertyActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case PropertyActionType.UpdateSuccess:
    case PropertyActionType.InsertSuccess: {
      return propertyAdapter.upsertOne(action.payload.property, state);
    }
    case PropertyActionType.UpdateError:
    case PropertyActionType.DeleteError:
    case PropertyActionType.InsertError: {
      return { ...state, loading: false, error: true };
    }

    case PropertyActionType.DeleteSuccess: {
      return propertyAdapter.removeOne(action.payload.property._id, state);
    }
    case PropertyActionType.LoadFailure: {
      return propertyAdapter.removeAll({
        ...state,
        error: true,
        loading: false,
        total: 0,
      });
    }
    default:
      return state;
  }
}
