import { Action } from '@ngrx/store';
import { PgroupParams } from '../models/pgroup.params';
import { PgroupResponse } from '../models/pgroup.response';
import { Pgroup } from '../models/pgroup';



export enum PgroupActionType {
  Loading = '[Pgroup] Loading',
  LoadSuccess = '[Pgroup] LoadSuccess',
  LoadFailure = '[Pgroup] LoadFailure',

  InsertSuccess = '[Pgroup] InsertSuccess',
  InsertLoading = '[Pgroup] InsertLoading',
  InsertError = '[Pgroup] InsertError',
  Insert = '[Pgroup] InsertPgroup',

  UpdateSuccess = '[Pgroup] UpdateSuccess',
  UpdateLoading = '[Pgroup] UpdateLoading',
  UpdateError = '[Pgroup] UpdateError',
  Update = '[Pgroup] UpdatePgroup',

  DeleteSuccess = '[Pgroup] DeleteSuccess',
  DeleteLoading = '[Pgroup] DeleteLoading',
  DeleteError = '[Pgroup] DeleteError',
  Delete = '[Pgroup] DeletePgroup',

}

export class PgroupLoadAction implements Action {
  public readonly type = PgroupActionType.Loading;

  constructor(public payload: PgroupParams) {
  }
}

export class PgroupInsertAction implements Action {
  public readonly type = PgroupActionType.Insert;
  constructor(public payload: PgroupParams) {}
}

export class PgroupInsertLoadingAction implements Action {
  public readonly type = PgroupActionType.InsertLoading;
  constructor(public payload: PgroupParams) {}
}

export class PgroupInsertSuccessAction implements Action {
  public readonly  type = PgroupActionType.InsertSuccess;
  constructor(public payload: PgroupResponse){}
 }

export class PgroupInsertErrorAction implements Action{
  public readonly type = PgroupActionType.InsertError;
  constructor(public error: any){}
}

export class PgroupLoadSuccessAction implements Action {
  public readonly type = PgroupActionType.LoadSuccess;
  constructor(public payload: PgroupResponse) {

  }
}


export class PgroupLoadFailAction implements Action {
  public readonly type = PgroupActionType.LoadFailure;
  constructor(public error: any) {
  }
}

// update
export class PgroupUpdateAction implements Action {
  public readonly type = PgroupActionType.Update;
  constructor(public payload: PgroupParams) {}
}



export class PgroupUpdateLoadingAction implements Action {
  public readonly type = PgroupActionType.UpdateLoading;
  constructor(public payload: PgroupParams) {}
}

export class PgroupUpdateSuccessAction implements Action {
  public readonly  type = PgroupActionType.UpdateSuccess;
  constructor(public payload: PgroupResponse){}
 }

export class PgroupUpdateErrorAction implements Action{
  public readonly type = PgroupActionType.UpdateError;
  constructor(public error: any){}
}

// update
export class PgroupDeleteAction implements Action {
  public readonly type = PgroupActionType.Delete;
  constructor(public payload: Pgroup) {}
}

export class PgroupDeleteLoadingAction implements Action {
  public readonly type = PgroupActionType.DeleteLoading;
  constructor(public payload: PgroupParams) {}
}

export class PgroupDeleteSuccessAction implements Action {
  public readonly  type = PgroupActionType.DeleteSuccess;
  constructor(public payload: PgroupResponse){}
 }

export class PgroupDeleteErrorAction implements Action{
  public readonly type = PgroupActionType.DeleteError;
  constructor(public error: any){}
}




export type PgroupAction = PgroupLoadAction
 | PgroupLoadSuccessAction
 | PgroupLoadFailAction

 |PgroupInsertSuccessAction
 |PgroupInsertErrorAction
 |PgroupInsertLoadingAction
 |PgroupInsertAction

 |PgroupUpdateSuccessAction
 |PgroupUpdateErrorAction
 |PgroupUpdateLoadingAction
 |PgroupUpdateAction

 |PgroupDeleteSuccessAction
 |PgroupDeleteErrorAction
 |PgroupDeleteLoadingAction
 |PgroupDeleteAction;
