
import { UserAction, UserActionType, UserLogoutAction } from './user.actions';
import { initialUserState, UserState, userAdapter } from './user.state';


export function userReducer(state = initialUserState, action: UserAction): UserState {
  switch (action.type) {
    case UserActionType.Loading: {
      return { ...state, loading: true };
    }

    case UserActionType.LoadSuccess: {
      return userAdapter.addOne(action.payload.user, {
        ...state,
        error:  '',
        loading: false,
      });
    }
    case UserActionType.DeleteLoading:
    case UserActionType.UpdateLoading:
    case UserActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case UserActionType.UpdateSuccess:
    case UserActionType.InsertSuccess: {
     // return userAdapter.upsertOne(action.payload.user, state);
     return { ...state, user: action.payload.user, token: action.payload.token};
    }

    case UserActionType.UpdateError:
    case UserActionType.DeleteError:
    case UserActionType.InsertError: {
      return { ...state, loading: false, error: `${action.error}-${new Date()}` };
    }
    case UserActionType.DeleteSuccess: {
      // return userAdapter.removeOne(action.payload.user._id, state);
      return{ ... state, user: null};
    }
    case UserActionType.LoadFailure: {
      return {...state, loading: false, error: action.error.message };
    }
    case UserActionType.LogOut: {
      return {...state, loading: false, user: null };
    }
    default:
      return state;
  }
}
