import { Component, OnInit } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { ActivatedRoute } from '@angular/router';
import { MapleService } from 'src/app/shared/products/models/MapleService';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.scss']
})
export class ServicesComponent implements OnInit {

  public service: MapleService;
  constructor( private appService: AppService, private activatedRoute: ActivatedRoute) {
    this.activatedRoute.params.subscribe(params => {
      const currentService = this.appService.getServices().filter(item => item.name == params.id);
      if (currentService){
        this.service = currentService[0];
      }
    });

   }

  ngOnInit(): void {
  }

}
