import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { EmailActionType,
  EmailLoadAction,
  EmailLoadSuccessAction,
  EmailLoadFailAction,
  EmailInsertSuccessAction,
  EmailInsertErrorAction,
  EmailDeleteSuccessAction,
  EmailDeleteErrorAction,
  EmailInsertAction} from './email.actions';
import { EmailService} from '../services/email.service';
import { EmailResponse } from '../models/email.response';
import { EmailParams } from '../models/email.params';
import { Email } from '../models/email';



@Injectable()
export class EmailEffects {
  constructor(private service: EmailService, private actions$: Actions) { }

  @Effect()
  public loadEmails$ = this.actions$
    .pipe(ofType<EmailLoadAction>(EmailActionType.Loading),
      map(action => action.payload),
      switchMap((params: EmailParams) =>
        this.service.getEmails(params).pipe(
          map((response: EmailResponse) => new EmailLoadSuccessAction(response)),
          catchError((error) => of(new EmailLoadFailAction(error))),
        ),
      ),
    );

    @Effect()
    public createEmails$ = this.actions$
      .pipe(ofType<EmailInsertAction>(EmailActionType.Insert),
        map(action => action.payload),
        switchMap((params: EmailParams) =>
          this.service.createEmail(params).pipe(
            map((response: EmailResponse) => new EmailInsertSuccessAction(response)),
            catchError((error) => of(new EmailInsertErrorAction(error))),
          ),
        ),
      );

      @Effect()
      public updateEmail$ = this.actions$
        .pipe(ofType<EmailLoadAction>(EmailActionType.Update),
          map(action => action.payload),
          switchMap((params: EmailParams) =>
            this.service.updateEmail(params).pipe(
              map((response: EmailResponse) => new EmailInsertSuccessAction(response)),
              catchError((error) => of(new EmailInsertErrorAction(error))),
            ),
          ),
        );

        @Effect()
        public deleteAmins$ = this.actions$
          .pipe(ofType<EmailLoadAction>(EmailActionType.Delete),
            map(action => action.payload),
            switchMap((email: EmailParams) =>
              this.service.deleteEmail(email.email).pipe(
                map((response: EmailResponse) => new EmailDeleteSuccessAction(response)),
                catchError((error) => of(new EmailDeleteErrorAction(error))),
              ),
            ),
          );
}
