import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { HunitParams } from '../models/hunit.params';
import { HunitResponse } from '../models/hunit.response';
import { Hunit } from '../models/hunit';
import { toFormData } from 'src/app/pages/properties/utils/to-formdata';
import { environment } from 'src/environments/environment.prod';


@Injectable()
export class HunitService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getHunits(params: HunitParams): Observable<HunitResponse> {
    let page = '1';
    let limit = '10';
    let parent = '';
    let parent2 = '';
    if (params.pageIndex){
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page){
       page = params.page.toString();
       limit = params.limit.toString();

    }
    if (params.property){
      parent = `&property=${params.property}`;
    }
    if (params._id){
      parent2 = `&_id=${params._id}`;
    }

    return this.httpClient.get<HunitResponse>(`${this.url}/hunit?page=${page}&limit=${limit}${parent}${parent2}`, {});
  }

  public createHunit(params: HunitParams): Observable<HunitResponse>{
    return this.httpClient.post<HunitResponse>(`${this.url}/hunit`, (params.hunit));
  }

  public updateHunit(params: HunitParams): Observable<HunitResponse> {
    return this.httpClient.put<HunitResponse>(`${this.url}/hunit`, params.hunit);
  }

  public deleteHunit(params: Hunit): Observable<HunitResponse>{
    return this.httpClient.delete<HunitResponse>(`${this.url}/hunit?_id=${params._id}`);
  }

}

