import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { PgroupActionType,
  PgroupLoadAction,
  PgroupLoadSuccessAction,
  PgroupLoadFailAction,
  PgroupInsertSuccessAction,
  PgroupInsertErrorAction,
  PgroupDeleteSuccessAction,
  PgroupDeleteErrorAction,
  PgroupInsertAction} from './pgroup.actions';
import { PgroupService} from '../services/pgroup.service';
import { PgroupResponse } from '../models/pgroup.response';
import { PgroupParams } from '../models/pgroup.params';
import { Pgroup } from '../models/pgroup';



@Injectable()
export class PgroupEffects {
  constructor(private service: PgroupService, private actions$: Actions) { }

  @Effect()
  public loadPgroups$ = this.actions$
    .pipe(ofType<PgroupLoadAction>(PgroupActionType.Loading),
      map(action => action.payload),
      switchMap((params: PgroupParams) =>
        this.service.getPgroups(params).pipe(
          map((response: PgroupResponse) => new PgroupLoadSuccessAction(response)),
          catchError((error) => of(new PgroupLoadFailAction(error))),
        ),
      ),
    );

    @Effect()
    public createPgroups$ = this.actions$
      .pipe(ofType<PgroupInsertAction>(PgroupActionType.Insert),
        map(action => action.payload),
        switchMap((params: PgroupParams) =>
          this.service.createPgroup(params).pipe(
            map((response: PgroupResponse) => new PgroupInsertSuccessAction(response)),
            catchError((error) => of(new PgroupInsertErrorAction(error))),
          ),
        ),
      );

      @Effect()
      public updatePgroup$ = this.actions$
        .pipe(ofType<PgroupLoadAction>(PgroupActionType.Update),
          map(action => action.payload),
          switchMap((params: PgroupParams) =>
            this.service.updatePgroup(params).pipe(
              map((response: PgroupResponse) => new PgroupInsertSuccessAction(response)),
              catchError((error) => of(new PgroupInsertErrorAction(error))),
            ),
          ),
        );

        @Effect()
        public deleteAmins$ = this.actions$
          .pipe(ofType<PgroupLoadAction>(PgroupActionType.Delete),
            map(action => action.payload),
            switchMap((pgroup: Pgroup) =>
              this.service.deletePgroup(pgroup).pipe(
                map((response: PgroupResponse) => new PgroupDeleteSuccessAction(response)),
                catchError((error) => of(new PgroupDeleteErrorAction(error))),
              ),
            ),
          );
}
