import { Action } from '@ngrx/store';
import { FeedbackParams } from '../models/feedback.params';
import { FeedbackResponse } from '../models/feedback.response';
import { Feedback } from '../models/feedback';



export enum FeedbackActionType {
  Loading = '[Feedback] Loading',
  LoadSuccess = '[Feedback] LoadSuccess',
  LoadFailure = '[Feedback] LoadFailure',

  InsertSuccess = '[Feedback] InsertSuccess',
  InsertLoading = '[Feedback] InsertLoading',
  InsertError = '[Feedback] InsertError',
  Insert = '[Feedback] InsertFeedback',

  UpdateSuccess = '[Feedback] UpdateSuccess',
  UpdateLoading = '[Feedback] UpdateLoading',
  UpdateError = '[Feedback] UpdateError',
  Update = '[Feedback] UpdateFeedback',

  DeleteSuccess = '[Feedback] DeleteSuccess',
  DeleteLoading = '[Feedback] DeleteLoading',
  DeleteError = '[Feedback] DeleteError',
  Delete = '[Feedback] DeleteFeedback',

}

export class FeedbackLoadAction implements Action {
  public readonly type = FeedbackActionType.Loading;

  constructor(public payload: FeedbackParams) {
  }
}

export class FeedbackInsertAction implements Action {
  public readonly type = FeedbackActionType.Insert;
  constructor(public payload: FeedbackParams) {}
}

export class FeedbackInsertLoadingAction implements Action {
  public readonly type = FeedbackActionType.InsertLoading;
  constructor(public payload: FeedbackParams) {}
}

export class FeedbackInsertSuccessAction implements Action {
  public readonly  type = FeedbackActionType.InsertSuccess;
  constructor(public payload: FeedbackResponse){}
 }

export class FeedbackInsertErrorAction implements Action{
  public readonly type = FeedbackActionType.InsertError;
  constructor(public error: any){}
}

export class FeedbackLoadSuccessAction implements Action {
  public readonly type = FeedbackActionType.LoadSuccess;
  constructor(public payload: FeedbackResponse) {

  }
}


export class FeedbackLoadFailAction implements Action {
  public readonly type = FeedbackActionType.LoadFailure;
  constructor(public error: any) {
  }
}

// update
export class FeedbackUpdateAction implements Action {
  public readonly type = FeedbackActionType.Update;
  constructor(public payload: FeedbackParams) {}
}



export class FeedbackUpdateLoadingAction implements Action {
  public readonly type = FeedbackActionType.UpdateLoading;
  constructor(public payload: FeedbackParams) {}
}

export class FeedbackUpdateSuccessAction implements Action {
  public readonly  type = FeedbackActionType.UpdateSuccess;
  constructor(public payload: FeedbackResponse){}
 }

export class FeedbackUpdateErrorAction implements Action{
  public readonly type = FeedbackActionType.UpdateError;
  constructor(public error: any){}
}

// update
export class FeedbackDeleteAction implements Action {
  public readonly type = FeedbackActionType.Delete;
  constructor(public payload: Feedback) {}
}

export class FeedbackDeleteLoadingAction implements Action {
  public readonly type = FeedbackActionType.DeleteLoading;
  constructor(public payload: FeedbackParams) {}
}

export class FeedbackDeleteSuccessAction implements Action {
  public readonly  type = FeedbackActionType.DeleteSuccess;
  constructor(public payload: FeedbackResponse){}
 }

export class FeedbackDeleteErrorAction implements Action{
  public readonly type = FeedbackActionType.DeleteError;
  constructor(public error: any){}
}




export type FeedbackAction = FeedbackLoadAction
 | FeedbackLoadSuccessAction
 | FeedbackLoadFailAction

 |FeedbackInsertSuccessAction
 |FeedbackInsertErrorAction
 |FeedbackInsertLoadingAction
 |FeedbackInsertAction

 |FeedbackUpdateSuccessAction
 |FeedbackUpdateErrorAction
 |FeedbackUpdateLoadingAction
 |FeedbackUpdateAction

 |FeedbackDeleteSuccessAction
 |FeedbackDeleteErrorAction
 |FeedbackDeleteLoadingAction
 |FeedbackDeleteAction;
