
import { createSelector, createFeatureSelector } from '@ngrx/store';
import { ownerAdapter, OwnerState } from './owner.state';


export const {
  selectIds: _selectOwnerDataIds,
  selectEntities: _selectOwnerEntities,
  selectAll: _selectAllOwner,
  selectTotal: _selectOwnerTotal,
} = ownerAdapter.getSelectors();

export const selectOwnerState = createFeatureSelector<OwnerState>('owner');

export const selectOwnerIds = createSelector(
  selectOwnerState,
  _selectOwnerDataIds,
);

export const selectOwnerEntities = createSelector(
  selectOwnerState,
  _selectOwnerEntities,
);

export const selectAllOwner = createSelector(
  selectOwnerState,
  _selectAllOwner,
);

export const selectOwnerError = createSelector(
  selectOwnerState,
  (state: OwnerState): boolean => state.error,
);

export const selectOwnerLoading = createSelector(
  selectOwnerState,
  (state: OwnerState): boolean => state.loading,
);

export const selectOwnerTotal = createSelector(
  selectOwnerState,
  (state: OwnerState): number => state.total,
);
