import { NgModule } from '@angular/core';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { environment } from '../../environments/environment';
import { reducers } from '.';
import { PropertyEffects} from './../pages/properties/store/property.effects';
import {UserEffects} from './../pages/register/store/user.effects';
import { HunitEffects } from './../pages/account/housingunit/store/hunit.effects';
import { BillEffects } from './../shared/bills/store/bill.effects';
import { PgroupEffects } from './../shared/pgroup/store/pgroup.effects';
import { PaymentEffects } from './../shared/payment/store/payment.effects';
import { TenantEffects } from './../shared/tenants/store/tenant.effects';
import { PublicationEffects } from './../shared/publication/store/publication.effects';
import { EmailEffects} from './../shared/email/store/email.effects';
import {FeedbackEffects } from './../shared/feedback/store/feedback.effects';


@NgModule({
  imports: [
    StoreModule.forRoot(reducers),
    EffectsModule.forRoot([PropertyEffects,
        UserEffects, HunitEffects ,
        BillEffects, PgroupEffects,
        PaymentEffects, TenantEffects,
        PublicationEffects, EmailEffects,
        FeedbackEffects]),
    StoreDevtoolsModule.instrument({ maxAge: 25, logOnly: environment.production }),
  ],
  exports: [StoreModule],
})
export class StorageModule { }
