import { Component, OnInit, Input } from '@angular/core';
import { AppSettings, Settings } from 'src/app/app.settings';
import { DomSanitizer, SafeStyle } from '@angular/platform-browser';
import { Property } from 'src/app/app.models';
import { GlobalState } from 'src/app/store/states/global.state';
import { Store } from '@ngrx/store';
import { pipe } from 'rxjs';
import { selectAllPropertyDevelopment } from 'src/app/pages/properties/store/developments/property.developments.selectors';
import { SwiperConfigInterface } from 'ngx-swiper-wrapper';
import { Router } from '@angular/router';

@Component({
  selector: 'app-developments',
  templateUrl: './developments.component.html',
  styleUrls: ['./developments.component.scss'],
})
export class DevelopmentsComponent implements OnInit {

  constructor(public appSettings: AppSettings,
              private sanitizer: DomSanitizer,
              private store: Store<GlobalState>,
              private router: Router,

    ) {
    this.settings = this.appSettings.settings;
    setTimeout(() => {
      this.settings.headerBgImage = true;
    });
  }

  @Input('backgroundImage') backgroundImage;
  @Input('images') images: string[];
  @Input('message') message: string[];
  public config: SwiperConfigInterface = { };

  public settings: Settings;
  public bgImage;
  public properties: Property[];
  public bgImages: SafeStyle[ ] = [
   this.wrap('https://images.pexels.com/photos/584399/living-room-couch-interior-room-584399.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940'),
   this.wrap('assets/images/props/outside/outside_2.jpg'),
   this.wrap('assets/images/props/outside/outside_3.jpg'),
   this.wrap('assets/images/props/outside/outside_4.jpg'),
   this.wrap('assets/images/props/outside/outside_5.jpg'),
   this.wrap('assets/images/props/outside/outside_6.jpg'),
  ];

   public namePair: NamePair[] = [
     {
      title: 'Nakuru 001',
      description: '',
      image: 'https://images.pexels.com/photos/584399/living-room-couch-interior-room-584399.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940',
      },
     {title: 'Nakuru 002', description: ''},
     {title: 'Nakuru 003', description: ''},
     {title: 'Nakuru 004 ', description: ''},
     {title: 'Nakuru 005 ', description: ''},
     {title: 'Nakuru 006 ', description: ''},
     {title: 'Nakuru 007 ', description: ''},
     {title: 'Nakuru 008 ', description: ''},
     {title: 'Nakuru 009 ', description: ''},

   ];


  ngOnInit(): void {
    if (this.backgroundImage) {
      this.bgImage = this.sanitizer.bypassSecurityTrustStyle('url(' + this.backgroundImage + ')');
    }

    this.store.select(pipe(selectAllPropertyDevelopment)).subscribe(props => {
      this.properties = props;


    });

  }

  private wrap(url: string): SafeStyle {
    return this.sanitizer.bypassSecurityTrustStyle('url(' + url + ')');
  }

  public goToRent() {
    this.router.navigateByUrl('/sale/rent');
  }


  ngAfterViewInit() {
    this.config = {
      observer: true,
      slidesPerView: 7,
      spaceBetween: 16,
      keyboard: true,
      navigation: true,
      pagination: true,
      grabCursor: true,
      loop: false,
      preloadImages: false,
      lazy: true,
      autoplay: {
        delay: 4500,
        disableOnInteraction: false,
      },
      speed: 500,
      effect: 'slide',
      breakpoints: {
        320: {
          slidesPerView: 1,
        },
        480: {
          slidesPerView: 1,
        },
        600: {
          slidesPerView: 1,
        },
        960: {
          slidesPerView:  1,
        },
        1280: {
          slidesPerView: 2,
        },
        1500: {
          slidesPerView: 3,
        },
      },
    };
  }

}

export interface NamePair {
  title?: string;
  description?: string;
  image?: string;
}
