
import { PublicationAction, PublicationActionType } from './publication.actions';
import { initialPublicationState, PublicationState, publicationAdapter } from './publication.state';


export function publicationReducer(state = initialPublicationState, action: PublicationAction): PublicationState {
  switch (action.type) {
    case PublicationActionType.Loading: {
      return { ...state, loading: true };
    }

    case PublicationActionType.LoadSuccess: {
      if (action.payload.publication){
        return publicationAdapter.upsertOne(action.payload.publication, state);
      }

      return publicationAdapter.setAll(action.payload.publications.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.publications.page,
        total: action.payload.publications.totalDocs,
      });
    }
    case PublicationActionType.DeleteLoading:
    case PublicationActionType.UpdateLoading:
    case PublicationActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case PublicationActionType.UpdateSuccess:
    case PublicationActionType.InsertSuccess: {
    return publicationAdapter.upsertOne(action.payload.publication, state);
    }
    case PublicationActionType.UpdateError:
    case PublicationActionType.DeleteError:
    case PublicationActionType.InsertError: {
      return { ...state, loading: false, error: true };
    }

    case PublicationActionType.DeleteSuccess: {
      return publicationAdapter.removeOne(action.payload.publication._id, state);
    }
    case PublicationActionType.LoadFailure: {
      return publicationAdapter.removeAll({
        ...state,
        error: true,
        loading: false,
        total: 0,
      });
    }
    default:
      return state;
  }
}
