import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { UserActionType,
  UserLoadAction,
  UserLoadSuccessAction,
  UserLoadFailAction,
  UserInsertSuccessAction,
  UserInsertErrorAction,
  UserDeleteSuccessAction,
  UserDeleteErrorAction,
  UserInsertAction,
  UserLoginAction} from './user.actions';
import { UserService } from '../services/user.service';
import { UserParams } from '../models/user-params';
import { UserResponse } from '../models/user-response';
import { User } from '../models/user';
import { HttpErrorResponse } from '@angular/common/http';

@Injectable()
export class UserEffects {
  constructor(private service: UserService, private actions$: Actions) { }

  @Effect()
  public loadUsers$ = this.actions$
    .pipe(ofType<UserLoadAction>(UserActionType.Loading),
      map(action => action.payload),
      switchMap((params: UserParams) =>
        this.service.getUsers(params).pipe(
          map((response: UserResponse) => new UserLoadSuccessAction(response)),
          catchError((error) => of(new UserLoadFailAction(error))),
        ),
      ),
    );

    @Effect()
    public createUsers$ = this.actions$
      .pipe(ofType<UserInsertAction>(UserActionType.Insert),
        map(action => action.payload),
        switchMap((params: User) =>
          this.service.createUser(params).pipe(
            map((response: UserResponse) => new UserInsertSuccessAction(response)),
            catchError((error) => of(new UserInsertErrorAction(error))),
          ),
        ),
      );


    @Effect()
    public loginUsers$ = this.actions$
      .pipe(ofType<UserLoginAction>(UserActionType.Login),
        map(action => action.payload),
        switchMap((user: User) =>
          this.service.loginUser(user).pipe(
            map((response: UserResponse) => new UserInsertSuccessAction(response)),
            catchError((error: HttpErrorResponse) => of(new UserInsertErrorAction(error))),
          ),
        ),
      );

      @Effect()
      public updateAmins$ = this.actions$
        .pipe(ofType<UserLoadAction>(UserActionType.Update),
          map(action => action.payload),
          switchMap((params: UserParams) =>
            this.service.updateUser(params).pipe(
              map((response: UserResponse) => new UserInsertSuccessAction(response)),
              catchError((error) => of(new UserInsertErrorAction(error))),
            ),
          ),
        );

        @Effect()
        public deleteAmins$ = this.actions$
          .pipe(ofType<UserLoadAction>(UserActionType.Delete),
            map(action => action.payload),
            switchMap((params: UserParams) =>
              this.service.deleteUser(params.user).pipe(
                map((response: UserResponse) => new UserDeleteSuccessAction(response)),
                catchError((error) => of(new UserDeleteErrorAction(error))),
              ),
            ),
          );
}
