
import { createSelector, createFeatureSelector } from '@ngrx/store';
import { feedbackAdapter, FeedbackState } from './feedback.state';


export const {
  selectIds: _selectFeedbackDataIds,
  selectEntities: _selectFeedbackEntities,
  selectAll: _selectAllFeedback,
  selectTotal: _selectFeedbackTotal,
} = feedbackAdapter.getSelectors();

export const selectFeedbackState = createFeatureSelector<FeedbackState>('feedback');

export const selectFeedbackIds = createSelector(
  selectFeedbackState,
  _selectFeedbackDataIds,
);

export const selectFeedbackEntities = createSelector(
  selectFeedbackState,
  _selectFeedbackEntities,
);

export const selectAllFeedback = createSelector(
  selectFeedbackState,
  _selectAllFeedback,
);

export const selectFeedbackError = createSelector(
  selectFeedbackState,
  (state: FeedbackState): boolean => state.error,
);

export const selectFeedbackLoading = createSelector(
  selectFeedbackState,
  (state: FeedbackState): boolean => state.loading,
);

export const selectFeedbackTotal = createSelector(
  selectFeedbackState,
  (state: FeedbackState): number => state.total,
);
