import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { TenantParams } from '../models/tenant.params';
import { TenantResponse } from '../models/tenant.response';
import { Tenant } from '../models/tenant';
import { environment } from 'src/environments/environment.prod';


@Injectable()
export class TenantService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getTenants(params: TenantParams): Observable<TenantResponse> {
    let page = '1';
    let limit = '10';
    let parent = '';
    if (params.pageIndex){
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page){
       page = params.page.toString();
       limit = params.limit.toString();
    }

    if (params.hunitId){
      parent = `&hunit=${params.hunitId}`;
    }

    return this.httpClient.get<TenantResponse>(`${this.url}/tenant?page=${page}&limit=${limit}${parent}`, {});
  }

  public createTenant(params: TenantParams): Observable<TenantResponse>{
    return this.httpClient.post<TenantResponse>(`${this.url}/tenant`, (params.tenant));
  }

  public updateTenant(params: TenantParams): Observable<TenantResponse> {
    return this.httpClient.put<TenantResponse>(`${this.url}/tenant`, params.tenant);
  }

  public deleteTenant(params: Tenant): Observable<TenantResponse>{
    return this.httpClient.delete<TenantResponse>(`${this.url}/tenant?_id=${params._id}`);
  }

}

