import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { BillParams } from '../models/bill.params';
import { BillResponse } from '../models/bill.response';
import { Bill } from '../models/bill';
import { environment } from 'src/environments/environment.prod';


@Injectable()
export class BillService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getBills(params: BillParams): Observable<BillResponse> {
    let page = '1';
    let limit = '10';
    let parent = '';
    if (params.pageIndex){
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page){
       page = params.page.toString();
       limit = params.limit.toString();
    }

    if (params.hunitId){
      parent = `&hunit=${params.hunitId}`;
    }

    return this.httpClient.get<BillResponse>(`${this.url}/bill?page=${page}&limit=${limit}${parent}`, {});
  }

  public createBill(params: BillParams): Observable<BillResponse>{
    return this.httpClient.post<BillResponse>(`${this.url}/bill`, (params.bill));
  }

  public updateBill(params: BillParams): Observable<BillResponse> {
    return this.httpClient.put<BillResponse>(`${this.url}/bill`, params.bill);
  }

  public deleteBill(params: Bill): Observable<BillResponse>{
    return this.httpClient.delete<BillResponse>(`${this.url}/bill?_id=${params._id}`);
  }

}

