import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Feedback } from '../models/feedback';




export interface FeedbackState extends EntityState<Feedback> {
  error: boolean;
  loading: boolean;
  total: number;
  page: number;
}

export const feedbackAdapter: EntityAdapter<Feedback> = createEntityAdapter<Feedback>({
  selectId: (feedback: Feedback) => feedback._id,
});

export const initialFeedbackState: FeedbackState = feedbackAdapter.getInitialState({
  error: false,
  loading: true,
  total: 0,
  page: 0,
});
