import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { PublicationActionType,
  PublicationLoadAction,
  PublicationLoadSuccessAction,
  PublicationLoadFailAction,
  PublicationInsertSuccessAction,
  PublicationInsertErrorAction,
  PublicationDeleteSuccessAction,
  PublicationDeleteErrorAction,
  PublicationInsertAction} from './publication.actions';
import { PublicationService} from '../services/publication.service';
import { PublicationResponse } from '../models/publication.response';
import { PublicationParams } from '../models/publication.params';
import { Publication } from '../models/publication';



@Injectable()
export class PublicationEffects {
  constructor(private service: PublicationService, private actions$: Actions) { }

  @Effect()
  public loadPublications$ = this.actions$
    .pipe(ofType<PublicationLoadAction>(PublicationActionType.Loading),
      map(action => action.payload),
      switchMap((params: PublicationParams) =>
        this.service.getPublications(params).pipe(
          map((response: PublicationResponse) => new PublicationLoadSuccessAction(response)),
          catchError((error) => of(new PublicationLoadFailAction(error))),
        ),
      ),
    );

    @Effect()
    public createPublications$ = this.actions$
      .pipe(ofType<PublicationInsertAction>(PublicationActionType.Insert),
        map(action => action.payload),
        switchMap((params: PublicationParams) =>
          this.service.createPublication(params).pipe(
            map((response: PublicationResponse) => new PublicationInsertSuccessAction(response)),
            catchError((error) => of(new PublicationInsertErrorAction(error))),
          ),
        ),
      );

      @Effect()
      public updatePublication$ = this.actions$
        .pipe(ofType<PublicationLoadAction>(PublicationActionType.Update),
          map(action => action.payload),
          switchMap((params: PublicationParams) =>
            this.service.updatePublication(params).pipe(
              map((response: PublicationResponse) => new PublicationInsertSuccessAction(response)),
              catchError((error) => of(new PublicationInsertErrorAction(error))),
            ),
          ),
        );

        @Effect()
        public deleteAmins$ = this.actions$
          .pipe(ofType<PublicationLoadAction>(PublicationActionType.Delete),
            map(action => action.payload),
            switchMap((publication: Publication) =>
              this.service.deletePublication(publication).pipe(
                map((response: PublicationResponse) => new PublicationDeleteSuccessAction(response)),
                catchError((error) => of(new PublicationDeleteErrorAction(error))),
              ),
            ),
          );
}
