import { Pipe, PipeTransform } from '@angular/core';
import { environment } from 'src/environments/environment.prod';
import { DomSanitizer, SafeStyle } from '@angular/platform-browser';


@Pipe({
  name: 'img'
})
export class ImgPipe implements PipeTransform {

  constructor(private sanitizer: DomSanitizer){

  }
  transform(value: string, ...args: unknown[]): SafeStyle{
    if (value){
     console.log(value);
     let result = '';
     if (!value.startsWith('http')){
      const arrayString: string[] = value.split('/');
      const imageName = arrayString[arrayString.length - 1];
      result = `${environment.apiBaseUrl}/images/users/${imageName}`;
     }else{
       result = value;
     }
     return this.sanitizer.bypassSecurityTrustStyle('url(' + result + ')');
    }else{
      return '';
    }
  }

}
