import { Action } from '@ngrx/store';
import { PaymentParams } from '../models/payment.params';
import { PaymentResponse } from '../models/payment.response';
import { Payment } from '../models/payment';



export enum PaymentActionType {
  Loading = '[Payment] Loading',
  LoadSuccess = '[Payment] LoadSuccess',
  LoadFailure = '[Payment] LoadFailure',

  InsertSuccess = '[Payment] InsertSuccess',
  InsertLoading = '[Payment] InsertLoading',
  InsertError = '[Payment] InsertError',
  Insert = '[Payment] InsertPayment',

  UpdateSuccess = '[Payment] UpdateSuccess',
  UpdateLoading = '[Payment] UpdateLoading',
  UpdateError = '[Payment] UpdateError',
  Update = '[Payment] UpdatePayment',

  DeleteSuccess = '[Payment] DeleteSuccess',
  DeleteLoading = '[Payment] DeleteLoading',
  DeleteError = '[Payment] DeleteError',
  Delete = '[Payment] DeletePayment',

}

export class PaymentLoadAction implements Action {
  public readonly type = PaymentActionType.Loading;

  constructor(public payload: PaymentParams) {
  }
}

export class PaymentInsertAction implements Action {
  public readonly type = PaymentActionType.Insert;
  constructor(public payload: PaymentParams) {}
}

export class PaymentInsertLoadingAction implements Action {
  public readonly type = PaymentActionType.InsertLoading;
  constructor(public payload: PaymentParams) {}
}

export class PaymentInsertSuccessAction implements Action {
  public readonly  type = PaymentActionType.InsertSuccess;
  constructor(public payload: PaymentResponse){}
 }

export class PaymentInsertErrorAction implements Action{
  public readonly type = PaymentActionType.InsertError;
  constructor(public error: any){}
}

export class PaymentLoadSuccessAction implements Action {
  public readonly type = PaymentActionType.LoadSuccess;
  constructor(public payload: PaymentResponse) {

  }
}


export class PaymentLoadFailAction implements Action {
  public readonly type = PaymentActionType.LoadFailure;
  constructor(public error: any) {
  }
}

// update
export class PaymentUpdateAction implements Action {
  public readonly type = PaymentActionType.Update;
  constructor(public payload: PaymentParams) {}
}



export class PaymentUpdateLoadingAction implements Action {
  public readonly type = PaymentActionType.UpdateLoading;
  constructor(public payload: PaymentParams) {}
}

export class PaymentUpdateSuccessAction implements Action {
  public readonly  type = PaymentActionType.UpdateSuccess;
  constructor(public payload: PaymentResponse){}
 }

export class PaymentUpdateErrorAction implements Action{
  public readonly type = PaymentActionType.UpdateError;
  constructor(public error: any){}
}

// update
export class PaymentDeleteAction implements Action {
  public readonly type = PaymentActionType.Delete;
  constructor(public payload: Payment) {}
}

export class PaymentDeleteLoadingAction implements Action {
  public readonly type = PaymentActionType.DeleteLoading;
  constructor(public payload: PaymentParams) {}
}

export class PaymentDeleteSuccessAction implements Action {
  public readonly  type = PaymentActionType.DeleteSuccess;
  constructor(public payload: PaymentResponse){}
 }

export class PaymentDeleteErrorAction implements Action{
  public readonly type = PaymentActionType.DeleteError;
  constructor(public error: any){}
}




export type PaymentAction = PaymentLoadAction
 | PaymentLoadSuccessAction
 | PaymentLoadFailAction

 |PaymentInsertSuccessAction
 |PaymentInsertErrorAction
 |PaymentInsertLoadingAction
 |PaymentInsertAction

 |PaymentUpdateSuccessAction
 |PaymentUpdateErrorAction
 |PaymentUpdateLoadingAction
 |PaymentUpdateAction

 |PaymentDeleteSuccessAction
 |PaymentDeleteErrorAction
 |PaymentDeleteLoadingAction
 |PaymentDeleteAction;
