import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Property } from '../../../app.models';



export interface PropertyState extends EntityState<Property> {
  error: boolean;
  loading: boolean;
  total: number;
  page: number;
}

export const propertyAdapter: EntityAdapter<Property> = createEntityAdapter<Property>({
  selectId: (property: Property) => property._id,
});

export const initialPropertyState: PropertyState = propertyAdapter.getInitialState({
  error: false,
  loading: true,
  total: 0,
  page: 0,
});
