import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormGroupDirective } from '@angular/forms';
import { emailValidator } from '../../utils/app-validators';
import { ContactService } from 'src/app/pages/contact/services/contact.service';
import { Contact } from 'src/app/pages/contact/model/contact';
import { NotificationService } from 'src/app/services/notification.service';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {
  public feedbackForm: FormGroup;
  public subscribeForm: FormGroup;
  @ViewChild(FormGroupDirective) formGroupDirective: FormGroupDirective;
  constructor(public formBuilder: FormBuilder, private contactService: ContactService, private notify: NotificationService) { }

  ngOnInit() {
    this.feedbackForm = this.formBuilder.group({
      email: ['', Validators.compose([Validators.required, emailValidator])],
    });
    this.subscribeForm = this.formBuilder.group({
      email: ['', Validators.compose([Validators.required, emailValidator])]
    });
  }

  public onFeedbackFormSubmit(values: Object): void {
      if (this.feedbackForm.valid) {
        const contact: Contact = this.feedbackForm.value as Contact;
        this.contactService.addToMailingList(contact.email).subscribe(
          (val) => {
             this.notify.openSnackBar('You have successfully subcribed to updates', 'close');
             setTimeout(() =>
             this.formGroupDirective.resetForm(), 0);
          },
          response => {
            this.notify.openSnackBar('Error could not save email', 'close');
          },
          () => {
              console.log('The PATCH observable is now completed.');
          });


    }
  }

  public onSubscribeFormSubmit(values: Object): void {
    if (this.subscribeForm.valid) {
      console.log(values);
    }
  }

}
