import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Owner } from '../models/owner';




export interface OwnerState extends EntityState<Owner> {
  error: boolean;
  loading: boolean;
  total: number;
  page: number;
}

export const ownerAdapter: EntityAdapter<Owner> = createEntityAdapter<Owner>({
  selectId: (owner: Owner) => owner._id,
});

export const initialOwnerState: OwnerState = ownerAdapter.getInitialState({
  error: false,
  loading: true,
  total: 0,
  page: 0,
});
