import { Action } from '@ngrx/store';
import { PublicationParams } from '../models/publication.params';
import { PublicationResponse } from '../models/publication.response';
import { Publication } from '../models/publication';



export enum PublicationActionType {
  Loading = '[Publication] Loading',
  LoadSuccess = '[Publication] LoadSuccess',
  LoadFailure = '[Publication] LoadFailure',

  InsertSuccess = '[Publication] InsertSuccess',
  InsertLoading = '[Publication] InsertLoading',
  InsertError = '[Publication] InsertError',
  Insert = '[Publication] InsertPublication',

  UpdateSuccess = '[Publication] UpdateSuccess',
  UpdateLoading = '[Publication] UpdateLoading',
  UpdateError = '[Publication] UpdateError',
  Update = '[Publication] UpdatePublication',

  DeleteSuccess = '[Publication] DeleteSuccess',
  DeleteLoading = '[Publication] DeleteLoading',
  DeleteError = '[Publication] DeleteError',
  Delete = '[Publication] DeletePublication',

}

export class PublicationLoadAction implements Action {
  public readonly type = PublicationActionType.Loading;

  constructor(public payload: PublicationParams) {
  }
}

