import { createSelector, createFeatureSelector } from '@ngrx/store';

import { PropertySearchState, propertyAdapter } from './property.search.state';


export const {
  selectIds: _selectPropertySearchDataIds,
  selectEntities: _selectPropertySearchEntities,
  selectAll: _selectAllPropertySearch,
  selectTotal: _selectPropertySearchTotal,
} = propertyAdapter.getSelectors();

export const selectPropertySearchSearchState = createFeatureSelector<PropertySearchState>('propertySearch');

export const selectPropertySearchIds = createSelector(
  selectPropertySearchSearchState,
  _selectPropertySearchDataIds,
);

export const selectPropertySearchEntities = createSelector(
  selectPropertySearchSearchState,
  _selectPropertySearchEntities,
);

export const selectAllPropertySearch = createSelector(
  selectPropertySearchSearchState,
  _selectAllPropertySearch,
);

export const selectPropertySearchError = createSelector(
  selectPropertySearchSearchState,
  (state: PropertySearchState): boolean => state.error,
);

export const selectPropertySearchLoading = createSelector(
  selectPropertySearchSearchState,
  (state: PropertySearchState): boolean => state.loading,
);

export const selectPropertySearchTotal = createSelector(
  selectPropertySearchSearchState,
  (state: PropertySearchState): number => state.total,
);
