import { Action } from '@ngrx/store';
import { BillParams } from '../models/bill.params';
import { BillResponse } from '../models/bill.response';
import { Bill } from '../models/bill';



export enum BillActionType {
  Loading = '[Bill] Loading',
  LoadSuccess = '[Bill] LoadSuccess',
  LoadFailure = '[Bill] LoadFailure',

  InsertSuccess = '[Bill] InsertSuccess',
  InsertLoading = '[Bill] InsertLoading',
  InsertError = '[Bill] InsertError',
  Insert = '[Bill] InsertBill',

  UpdateSuccess = '[Bill] UpdateSuccess',
  UpdateLoading = '[Bill] UpdateLoading',
  UpdateError = '[Bill] UpdateError',
  Update = '[Bill] UpdateBill',

  DeleteSuccess = '[Bill] DeleteSuccess',
  DeleteLoading = '[Bill] DeleteLoading',
  DeleteError = '[Bill] DeleteError',
  Delete = '[Bill] DeleteBill',

}

export class BillLoadAction implements Action {
  public readonly type = BillActionType.Loading;

  constructor(public payload: BillParams) {
  }
}

export class BillInsertAction implements Action {
  public readonly type = BillActionType.Insert;
  constructor(public payload: BillParams) {}
}

export class BillInsertLoadingAction implements Action {
  public readonly type = BillActionType.InsertLoading;
  constructor(public payload: BillParams) {}
}

export class BillInsertSuccessAction implements Action {
  public readonly  type = BillActionType.InsertSuccess;
  constructor(public payload: BillResponse){}
 }

export class BillInsertErrorAction implements Action{
  public readonly type = BillActionType.InsertError;
  constructor(public error: any){}
}

export class BillLoadSuccessAction implements Action {
  public readonly type = BillActionType.LoadSuccess;
  constructor(public payload: BillResponse) {

  }
}


export class BillLoadFailAction implements Action {
  public readonly type = BillActionType.LoadFailure;
  constructor(public error: any) {
  }
}

// update
export class BillUpdateAction implements Action {
  public readonly type = BillActionType.Update;
  constructor(public payload: BillParams) {}
}



export class BillUpdateLoadingAction implements Action {
  public readonly type = BillActionType.UpdateLoading;
  constructor(public payload: BillParams) {}
}

export class BillUpdateSuccessAction implements Action {
  public readonly  type = BillActionType.UpdateSuccess;
  constructor(public payload: BillResponse){}
 }

export class BillUpdateErrorAction implements Action{
  public readonly type = BillActionType.UpdateError;
  constructor(public error: any){}
}

// update
export class BillDeleteAction implements Action {
  public readonly type = BillActionType.Delete;
  constructor(public payload: Bill) {}
}

export class BillDeleteLoadingAction implements Action {
  public readonly type = BillActionType.DeleteLoading;
  constructor(public payload: BillParams) {}
}

export class BillDeleteSuccessAction implements Action {
  public readonly  type = BillActionType.DeleteSuccess;
  constructor(public payload: BillResponse){}
 }

export class BillDeleteErrorAction implements Action{
  public readonly type = BillActionType.DeleteError;
  constructor(public error: any){}
}




export type BillAction = BillLoadAction
 | BillLoadSuccessAction
 | BillLoadFailAction

 |BillInsertSuccessAction
 |BillInsertErrorAction
 |BillInsertLoadingAction
 |BillInsertAction

 |BillUpdateSuccessAction
 |BillUpdateErrorAction
 |BillUpdateLoadingAction
 |BillUpdateAction

 |BillDeleteSuccessAction
 |BillDeleteErrorAction
 |BillDeleteLoadingAction
 |BillDeleteAction;
