import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Pgroup } from '../models/pgroup';




export interface PgroupState extends EntityState<Pgroup> {
  error: boolean;
  loading: boolean;
  total: number;
  page: number;
}

export const pgroupAdapter: EntityAdapter<Pgroup> = createEntityAdapter<Pgroup>({
  selectId: (pgroup: Pgroup) => pgroup._id,
});

export const initialPgroupState: PgroupState = pgroupAdapter.getInitialState({
  error: false,
  loading: true,
  total: 0,
  page: 0,
});
