import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { PaymentActionType,
  PaymentLoadAction,
  PaymentLoadSuccessAction,
  PaymentLoadFailAction,
  PaymentInsertSuccessAction,
  PaymentInsertErrorAction,
  PaymentDeleteSuccessAction,
  PaymentDeleteErrorAction,
  PaymentInsertAction} from './payment.actions';
import { PaymentService } from '../services/payment.service';
import { PaymentResponse } from '../models/payment.response';
import { PaymentParams } from '../models/payment.params';
import { Payment } from '../models/payment';



@Injectable()
export class PaymentEffects {
  constructor(private service: PaymentService, private actions$: Actions) { }

  @Effect()
  public loadPayments$ = this.actions$
    .pipe(ofType<PaymentLoadAction>(PaymentActionType.Loading),
      map(action => action.payload),
      switchMap((params: PaymentParams) =>
        this.service.getPayments(params).pipe(
          map((response: PaymentResponse) => new PaymentLoadSuccessAction(response)),
          catchError((error) => of(new PaymentLoadFailAction(error))),
        ),
      ),
    );

    @Effect()
    public createPayments$ = this.actions$
      .pipe(ofType<PaymentInsertAction>(PaymentActionType.Insert),
        map(action => action.payload),
        switchMap((params: PaymentParams) =>
          this.service.createPayment(params).pipe(
            map((response: PaymentResponse) => new PaymentInsertSuccessAction(response)),
            catchError((error) => of(new PaymentInsertErrorAction(error))),
          ),
        ),
      );

      @Effect()
      public updatePayment$ = this.actions$
        .pipe(ofType<PaymentLoadAction>(PaymentActionType.Update),
          map(action => action.payload),
          switchMap((params: PaymentParams) =>
            this.service.updatePayment(params).pipe(
              map((response: PaymentResponse) => new PaymentInsertSuccessAction(response)),
              catchError((error) => of(new PaymentInsertErrorAction(error))),
            ),
          ),
        );

        @Effect()
        public deleteAmins$ = this.actions$
          .pipe(ofType<PaymentLoadAction>(PaymentActionType.Delete),
            map(action => action.payload),
            switchMap((payment: Payment) =>
              this.service.deletePayment(payment).pipe(
                map((response: PaymentResponse) => new PaymentDeleteSuccessAction(response)),
                catchError((error) => of(new PaymentDeleteErrorAction(error))),
              ),
            ),
          );
}
