import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Bill } from '../models/bill';




export interface BillState extends EntityState<Bill> {
  error: boolean;
  loading: boolean;
  total: number;
  page: number;
}

export const billAdapter: EntityAdapter<Bill> = createEntityAdapter<Bill>({
  selectId: (bill: Bill) => bill._id,
});

export const initialBillState: BillState = billAdapter.getInitialState({
  error: false,
  loading: true,
  total: 0,
  page: 0,
});
