import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { HunitActionType,
  HunitLoadAction,
  HunitLoadSuccessAction,
  HunitLoadFailAction,
  HunitInsertSuccessAction,
  HunitInsertErrorAction,
  HunitDeleteSuccessAction,
  HunitDeleteErrorAction,
  HunitInsertAction} from './hunit.actions';
import { HunitService } from '../services/hunit.service';
import { HunitResponse } from '../models/hunit.response';
import { HunitParams } from '../models/hunit.params';
import { Hunit } from '../models/hunit';



@Injectable()
export class HunitEffects {
  constructor(private service: HunitService, private actions$: Actions) { }

  @Effect()
  public loadHunits$ = this.actions$
    .pipe(ofType<HunitLoadAction>(HunitActionType.Loading),
      map(action => action.payload),
      switchMap((params: HunitParams) =>
        this.service.getHunits(params).pipe(
          map((response: HunitResponse) => new HunitLoadSuccessAction(response)),
          catchError((error) => of(new HunitLoadFailAction(error))),
        ),
      ),
    );

    @Effect()
    public createHunits$ = this.actions$
      .pipe(ofType<HunitInsertAction>(HunitActionType.Insert),
        map(action => action.payload),
        switchMap((params: HunitParams) =>
          this.service.createHunit(params).pipe(
            map((response: HunitResponse) => new HunitInsertSuccessAction(response)),
            catchError((error) => of(new HunitInsertErrorAction(error))),
          ),
        ),
      );

      @Effect()
      public updateHunit$ = this.actions$
        .pipe(ofType<HunitLoadAction>(HunitActionType.Update),
          map(action => action.payload),
          switchMap((params: HunitParams) =>
            this.service.updateHunit(params).pipe(
              map((response: HunitResponse) => new HunitInsertSuccessAction(response)),
              catchError((error) => of(new HunitInsertErrorAction(error))),
            ),
          ),
        );

        @Effect()
        public deleteAmins$ = this.actions$
          .pipe(ofType<HunitLoadAction>(HunitActionType.Delete),
            map(action => action.payload),
            switchMap((hunit: HunitParams) =>
              this.service.deleteHunit(hunit.hunit).pipe(
                map((response: HunitResponse) => new HunitDeleteSuccessAction(response)),
                catchError((error) => of(new HunitDeleteErrorAction(error))),
              ),
            ),
          );
}
