import { Price } from 'src/app/app.models';
import { Feature } from './property.input';

export class PropertyLoad {
    id: number;
    title: string;
    desc: string;
    shortDesc: string;
    propertyType: string;
    propertyStatus: string[];
    city: string;
    zipCode: string;
    neighborhood: string[];
    street: string[];
    location: Location;
    formattedAddress: string;
    features: string[];
    featured: boolean;
    priceDollar: Price;
    priceEuro: Price;
    bedrooms: number;
    bathrooms: number;
    garages: number;
    area: Area;
    yearBuilt: number;
    ratingsCount: number;
    ratingsValue: number;
    additionalFeatures: Feature[];
    gallery: Gallery[];
    plans: Plan[];
    videos: Video[];
    published: Date;
    lastUpdate: Date;
    views: number;
}


export class Area {
    value: number;
    unit: string;
}

export class Gallery {
    small: string;
    medium: string;
    big: string;
}

export class Location {
    lat: number;
    lng: number;
}

export class Plan {
    name: string;
    desc: string;
    area: Area;
    rooms: number;
    baths: number;
    image: string;
}


export class Video {
    name: string;
    link: string;
}
