
import { PgroupAction, PgroupActionType } from './pgroup.actions';
import { initialPgroupState, PgroupState, pgroupAdapter } from './pgroup.state';


export function pgroupReducer(state = initialPgroupState, action: PgroupAction): PgroupState {
  switch (action.type) {
    case PgroupActionType.Loading: {
      return { ...state, loading: true };
    }

    case PgroupActionType.LoadSuccess: {
      if (action.payload.pgroup){
        return pgroupAdapter.upsertOne(action.payload.pgroup, state);
      }

      return pgroupAdapter.setAll(action.payload.pgroups.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.pgroups.page,
        total: action.payload.pgroups.totalDocs,
      });
    }
    case PgroupActionType.DeleteLoading:
    case PgroupActionType.UpdateLoading:
    case PgroupActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case PgroupActionType.UpdateSuccess:
    case PgroupActionType.InsertSuccess: {
    return pgroupAdapter.upsertOne(action.payload.pgroup, state);
    }
    case PgroupActionType.UpdateError:
    case PgroupActionType.DeleteError:
    case PgroupActionType.InsertError: {
      return { ...state, loading: false, error: true };
    }

    case PgroupActionType.DeleteSuccess: {
      return pgroupAdapter.removeOne(action.payload.pgroup._id, state);
    }
    case PgroupActionType.LoadFailure: {
      return pgroupAdapter.removeAll({
        ...state,
        error: true,
        loading: false,
        total: 0,
      });
    }
    default:
      return state;
  }
}
