import { Component, OnInit, ViewChild, Inject, NgZone, ElementRef } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Email } from '../models/email';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { Store, select } from '@ngrx/store';
import { EmailLoadAction, EmailDeleteAction, EmailInsertAction, EmailUpdateAction } from '../store/email.actions';
import { EmailService } from '../services/email.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { EmailParams } from '../models/email.params';
import { selectAllEmail, selectEmailTotal } from '../store/email.selectors';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppService } from '../../../app.service';
import { GlobalState } from '../../../store/states/global.state';
import { MapsAPILoader } from '@agm/core';


@Component({
  selector: 'app-email',
  templateUrl: './email.component.html',
  styleUrls: ['./email.component.scss'],
})
export class EmailComponent implements OnInit {
  displayedColumns: string[] = ['email', 'createdAt' ];
  dataSource: MatTableDataSource<Email>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public email: Email = {};
  private userId: string;


  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private emailService: EmailService,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.store.pipe(select(selectAllEmail)).subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });


    this.store.pipe(select(selectEmailTotal)).subscribe(total => {
        this.total = total;
       });

    this.loadEmail({page: 1, limit: 10});
    this.activatedRoute.params.subscribe(params => {
      this.userId = params.id;

    });

  }

 public openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
       duration: 2000,
    }).afterDismissed().subscribe(info => {
      if (info.dismissedByAction === true) {

      }
    });
 }

public loadEmail(data) {
   this.store.dispatch(new EmailLoadAction(data));
  }

  public remove(email: Email) {
    this.snackBar.open('Are you sure you want to delete?', 'DELETE', {
      duration: 2000,
   }).afterDismissed().subscribe(info => {
     if (info.dismissedByAction === true) {
      this.store.dispatch(new EmailDeleteAction(email));
      const index: number = this.dataSource.data.indexOf(email);
      if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Email>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
     }});


  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

openEditDialog(email: Email): void {

  const dialogRef = this.dialog.open(AddEmailComponent, {
    width: '500px',
    data: email,
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    console.log(result);

    this.email = result;
    this.email._id = email._id;

    const emailParams =  { email: this.email} as EmailParams;
    this.store.dispatch(new EmailUpdateAction(emailParams));

  });
}





openDialog(email?: any): void {
  const dialogRef = this.dialog.open(AddEmailComponent, {
    width: '500px',
    data: email,
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    console.log(result.file);
    if (result) {
      this.email = result;
      const emailParams =  { email: this.email} as EmailParams;
      this.store.dispatch(new EmailInsertAction(emailParams));
    }

  });
}

}

@Component({
  selector: 'app-add-email',
  templateUrl: './add-email.component.html',
  styleUrls: ['./add-email.component.scss'],
})
export class AddEmailComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<AddEmailComponent>,
    public formBuilder: FormBuilder,
    public store: Store<GlobalState>,
    private appService: AppService,
    @Inject(MAT_DIALOG_DATA)
    public data: Email) {

    }

    public title = 'Add Blog Post';
    public infoForm: FormGroup;

  public closeDialog() {
    this.dialogRef.close();
  }

  public onInfoFormSubmit(email: Email) {
    if (this.infoForm.valid) {
      this.data = email;
      this.dialogRef.close();
    }
  }

  ngOnInit(): void {

    this.infoForm = this.formBuilder.group({
      content: ['', Validators.required],
      file:  null,
      title: ['', Validators.compose([Validators.required, Validators.minLength(3)])],

    });

    if (this.data && this.data._id) {
      alert(JSON.stringify(this.data));

      this.infoForm.patchValue(this.data);
    }

  }

}

