
import { createSelector, createFeatureSelector } from '@ngrx/store';
import { billAdapter, BillState } from './bill.state';


export const {
  selectIds: _selectBillDataIds,
  selectEntities: _selectBillEntities,
  selectAll: _selectAllBill,
  selectTotal: _selectBillTotal,
} = billAdapter.getSelectors();

export const selectBillState = createFeatureSelector<BillState>('bill');

export const selectBillIds = createSelector(
  selectBillState,
  _selectBillDataIds,
);

export const selectBillEntities = createSelector(
  selectBillState,
  _selectBillEntities,
);

export const selectAllBill = createSelector(
  selectBillState,
  _selectAllBill,
);

export const selectBillError = createSelector(
  selectBillState,
  (state: BillState): boolean => state.error,
);

export const selectBillLoading = createSelector(
  selectBillState,
  (state: BillState): boolean => state.loading,
);

export const selectBillTotal = createSelector(
  selectBillState,
  (state: BillState): number => state.total,
);
