import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Bill } from '../models/bill';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { Store, select } from '@ngrx/store';
import { BillLoadAction, BillDeleteAction, BillInsertAction, BillUpdateAction } from '../store/bill.actions';
import { BillService } from '../services/bill.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BillParams } from '../models/bill.params';
import { Hunit } from '../../../pages/account/housingunit/models/hunit';
import { AppService } from 'src/app/app.service';
import { GlobalState } from 'src/app/store/states/global.state';
import { img } from 'src/app/pages/properties/utils/to-formdata';
import { selectAllBill, selectBillTotal } from './../store/bill.selectors';
import { MatSnackBar } from '@angular/material/snack-bar';
import { PropertyLoadAction } from 'src/app/pages/properties/store/property.actions';
import { AddPaymentComponent } from '../../payment/ui/payment.component';
import { Payment } from '../../payment/models/payment';
import { PaymentParams } from '../../payment/models/payment.params';
import { PaymentInsertAction } from '../../payment/store/payment.actions';
import { selectAllPayment } from '../../payment/store/payment.selectors';


@Component({
  selector: 'app-bill',
  templateUrl: './bill.component.html',
  styleUrls: ['./bill.component.scss']
})
export class BillComponent implements OnInit {
  displayedColumns: string[] = ['id', 'category', 'due', 'paid', 'amount', 'actions' ];
  dataSource: MatTableDataSource<Bill>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public bill: Bill = {};
  public payment: Payment = {};
  private hunitId: string;
  public hunit: Hunit = {};

  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private billService: BillService,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.store.pipe(select(selectAllBill)).subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });

    this.store.pipe(select(selectAllPayment)).subscribe(data => {
        this.loadBill({page: this.paginator.pageIndex, limit: 10, hunitId: this.hunitId});
      });



    this.store.pipe(select(selectBillTotal)).subscribe(total => {
        this.total = total;
       });


    this.activatedRoute.params.subscribe(params => {
      this.hunitId = params.id;
      this.loadBill({page: 1, limit: 10, hunitId: params.id});
      this.appService.getPropertyById(params.id).subscribe(hunit => {
        if (hunit){
          this.hunit = hunit;
        }

      });
    });

  }

  public getColor(bill: number, paid: number): string {
    const result = bill - paid;
    if (result === 0){
     return 'black';
    }else if (result < 0){
    return 'green';
  }else if ( result > 0 ) {
    return 'red';
  } else {
    return 'black';
  }
  }

 public openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
       duration: 2000,
    }).afterDismissed().subscribe(info => {
      if (info.dismissedByAction === true) {

      }
    });
 }

public loadBill(data){
   this.store.dispatch(new BillLoadAction(data));
  }

  public remove(bill: Bill) {
    this.snackBar.open('Are you sure you want to delete?', 'DELETE', {
      duration: 2000,
   }).afterDismissed().subscribe(info => {
     if (info.dismissedByAction === true) {
      this.store.dispatch(new BillDeleteAction(bill));
      const index: number = this.dataSource.data.indexOf(bill);
      if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Bill>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
     }});
  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

public img(url: string){
  if (url){
    return img(url);
  }
  return '';

}


openEditDialog(bill: Bill): void {
  const dialogRef = this.dialog.open(AddBillComponent, {
    width: '500px',
    data: bill
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');

    this.bill = result;
    this.bill._id = bill._id;
    this.bill.hunitId = this.hunitId;
    const billParams =  { bill: this.bill} as BillParams;
    this.store.dispatch(new BillUpdateAction(billParams));

  });
}

openPaymentDialog(bill: Bill): void {
  const dialogRef = this.dialog.open(AddPaymentComponent, {
    width: '500px',
    data: bill
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    if (result){
      this.payment = result;
      this.payment.hunitId = this.hunitId;
      this.payment.bill = bill;
      const paymentParams =  { payment: this.payment} as PaymentParams;
      this.store.dispatch(new PaymentInsertAction(paymentParams));
    }

  });
}
openDialog(): void {
  const dialogRef = this.dialog.open(AddBillComponent, {
    width: '500px',
    data: this.bill
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    if (result){
      this.bill = result;
      this.bill.hunitId = this.hunitId;
      const billParams =  { bill: this.bill} as BillParams;
      this.store.dispatch(new BillInsertAction(billParams));
    }

  });
}

}

@Component({
  selector: 'app-add-bill',
  templateUrl: './add-bill.component.html',
  styleUrls: ['./add-bill.component.scss']
})
export class AddBillComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<AddBillComponent>,
    public formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA)
    public data: Bill) {

    }
    public title = 'Add Bill';
    public category = [
      { id: 1, name: 'RENT' },
      { id: 2, name: 'WATER' },
      { id: 3, name: 'GARBAGE' },
      { id: 4, name: 'REPAIR' },
      { id: 5, name: 'GARBAGE' }
    ];
    public type =  { id: 1, name: 'RENT' };
    public infoForm: FormGroup;

  public closeDialog(){
    this.dialogRef.close();
  }
  public onInfoFormSubmit(bill: Bill){
    if (this.infoForm.valid){
      this.data = bill;
      this.dialogRef.close();
    }

  }

  ngOnInit(): void {

    this.infoForm = this.formBuilder.group({
      category: ['', Validators.required],
      refNumber: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      amount: [''],
      dueDate: [''],
      title: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      description: [''],

    });
    if (this.data && this.data._id){
      this.title = 'Edit Bill ' +   this.data.refNumber;
      this.infoForm.patchValue(this.data);
      this.type =   { id: 1, name: this.data.category};
    }

  }

}

