import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { FeedbackActionType,
  FeedbackLoadAction,
  FeedbackLoadSuccessAction,
  FeedbackLoadFailAction,
  FeedbackInsertSuccessAction,
  FeedbackInsertErrorAction,
  FeedbackDeleteSuccessAction,
  FeedbackDeleteErrorAction,
  FeedbackInsertAction} from './feedback.actions';
import { FeedbackService} from '../services/feedback.service';
import { FeedbackResponse } from '../models/feedback.response';
import { FeedbackParams } from '../models/feedback.params';
import { Feedback } from '../models/feedback';



@Injectable()
export class FeedbackEffects {
  constructor(private service: FeedbackService, private actions$: Actions) { }

  @Effect()
  public loadFeedbacks$ = this.actions$
    .pipe(ofType<FeedbackLoadAction>(FeedbackActionType.Loading),
      map(action => action.payload),
      switchMap((params: FeedbackParams) =>
        this.service.getFeedbacks(params).pipe(
          map((response: FeedbackResponse) => new FeedbackLoadSuccessAction(response)),
          catchError((error) => of(new FeedbackLoadFailAction(error))),
        ),
      ),
    );

    @Effect()
    public createFeedbacks$ = this.actions$
      .pipe(ofType<FeedbackInsertAction>(FeedbackActionType.Insert),
        map(action => action.payload),
        switchMap((params: FeedbackParams) =>
          this.service.createFeedback(params).pipe(
            map((response: FeedbackResponse) => new FeedbackInsertSuccessAction(response)),
            catchError((error) => of(new FeedbackInsertErrorAction(error))),
          ),
        ),
      );

      @Effect()
      public updateFeedback$ = this.actions$
        .pipe(ofType<FeedbackLoadAction>(FeedbackActionType.Update),
          map(action => action.payload),
          switchMap((params: FeedbackParams) =>
            this.service.updateFeedback(params).pipe(
              map((response: FeedbackResponse) => new FeedbackInsertSuccessAction(response)),
              catchError((error) => of(new FeedbackInsertErrorAction(error))),
            ),
          ),
        );

        @Effect()
        public deleteAmins$ = this.actions$
          .pipe(ofType<FeedbackLoadAction>(FeedbackActionType.Delete),
            map(action => action.payload),
            switchMap((feedback: Feedback) =>
              this.service.deleteFeedback(feedback).pipe(
                map((response: FeedbackResponse) => new FeedbackDeleteSuccessAction(response)),
                catchError((error) => of(new FeedbackDeleteErrorAction(error))),
              ),
            ),
          );
}
