
import { OwnerAction, OwnerActionType } from './owner.actions';
import { initialOwnerState, OwnerState, ownerAdapter } from './owner.state';


export function ownerReducer(state = initialOwnerState, action: OwnerAction): OwnerState {
  switch (action.type) {
    case OwnerActionType.Loading: {
      return { ...state, loading: true };
    }

    case OwnerActionType.LoadSuccess: {

      return ownerAdapter.addAll(action.payload.owners.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.owners.page,
        total: action.payload.owners.totalDocs,
      });
    }
    case OwnerActionType.DeleteLoading:
    case OwnerActionType.UpdateLoading:
    case OwnerActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case OwnerActionType.UpdateSuccess:
    case OwnerActionType.InsertSuccess: {
    return ownerAdapter.upsertOne(action.payload.owner, state);
    }
    case OwnerActionType.UpdateError:
    case OwnerActionType.DeleteError:
    case OwnerActionType.InsertError: {
      return { ...state, loading: false, error: true };
    }

    case OwnerActionType.DeleteSuccess: {
      return ownerAdapter.removeOne(action.payload.owner._id, state);
    }
    case OwnerActionType.LoadFailure: {
      return ownerAdapter.removeAll({
        ...state,
        error: true,
        loading: false,
        total: 0,
      });
    }
    default:
      return state;
  }
}
