import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Owner } from '../models/owner';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { Store, select } from '@ngrx/store';
import { OwnerLoadAction, OwnerDeleteAction, OwnerInsertAction, OwnerUpdateAction } from '../store/owner.actions';
import { OwnerService } from '../services/owner.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { OwnerParams } from '../models/owner.params';
import { Hunit } from '../../../pages/account/housingunit/models/hunit';
import { AppService } from 'src/app/app.service';
import { GlobalState } from 'src/app/store/states/global.state';
import { img } from 'src/app/pages/properties/utils/to-formdata';
import { selectAllOwner, selectOwnerTotal } from '../store/owner.selectors';
import { MatSnackBar } from '@angular/material/snack-bar';
import { PropertyLoadAction } from 'src/app/pages/properties/store/property.actions';
import { AddPaymentComponent } from '../../payment/ui/payment.component';
import { Payment } from '../../payment/models/payment';
import { PaymentParams } from '../../payment/models/payment.params';
import { PaymentInsertAction } from '../../payment/store/payment.actions';
import { User } from 'src/app/pages/register/models/user';
import { MatSelect } from '@angular/material/select';
import { ReplaySubject, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';


@Component({
  selector: 'app-owner',
  templateUrl: './owner.component.html',
  styleUrls: ['./owner.component.scss'],
})
export class OwnerComponent implements OnInit {
  displayedColumns: string[] = ['id', 'fullName', 'phone', 'ownerNumber', 'status', 'actions' ];
  dataSource: MatTableDataSource<Owner>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public owner: Owner = {};
  public payment: Payment = {};
  private hunitId: string;
  public hunit: Hunit = {};

  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private ownerService: OwnerService,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.store.pipe(select(selectAllOwner)).subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });


    this.store.pipe(select(selectOwnerTotal)).subscribe(total => {
        this.total = total;
       });


    this.activatedRoute.params.subscribe(params => {
      this.hunitId = params.id;
      this.loadOwner({page: 1, limit: 10, hunitId: params.id});
      this.appService.getPropertyById(params.id).subscribe(hunit => {
        if (hunit) {
          this.hunit = hunit;
        }

      });
    });

  }

 public openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
       duration: 2000,
    }).afterDismissed().subscribe(info => {
      if (info.dismissedByAction === true) {

      }
    });
 }

public loadOwner(data) {
   this.store.dispatch(new OwnerLoadAction(data));
  }

  public remove(owner: Owner) {
    this.snackBar.open('Are you sure you want to delete?', 'DELETE', {
      duration: 2000,
   }).afterDismissed().subscribe(info => {
     if (info.dismissedByAction === true) {
      this.store.dispatch(new OwnerDeleteAction(owner));
      const index: number = this.dataSource.data.indexOf(owner);
      if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Owner>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
     }});
  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

public img(url: string) {
  if (url) {
    return img(url);
  }
  return '';

}


openEditDialog(owner: Owner): void {
  const dialogRef = this.dialog.open(AddOwnerComponent, {
    width: '500px',
    data: owner,
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    alert(JSON.stringify(result));
    const user: User  = result;
    this.owner = {user, status: result.status, hunitId: this.hunitId};
    this.owner._id = owner._id;
    this.owner.hunitId = this.hunitId;
    const ownerParams =  { owner: this.owner} as OwnerParams;
    this.store.dispatch(new OwnerUpdateAction(ownerParams));

  });
}

openPaymentDialog(owner: Owner): void {
  const dialogRef = this.dialog.open(AddPaymentComponent, {
    width: '500px',
    data: owner,
  });


}
openDialog(): void {
  const dialogRef = this.dialog.open(AddOwnerComponent, {
    width: '500px',
    data: this.owner,
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    alert(JSON.stringify(result));
    console.log(result);
    if (result) {
      const user: User  = result;
      this.owner = {user, status: result.status, hunitId: this.hunitId};
      const ownerParams =  { owner: this.owner} as OwnerParams;
      this.store.dispatch(new OwnerInsertAction(ownerParams));
    }

  });
}

}

@Component({
  selector: 'app-add-owner',
  templateUrl: './add-owner.component.html',
  styleUrls: ['./add-owner.component.scss'],
})
export class AddOwnerComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<AddOwnerComponent>,
    public formBuilder: FormBuilder,
    public ownderService: OwnerService,
    @Inject(MAT_DIALOG_DATA)
    public data: Owner) {

    }

    @ViewChild('singleSelect', { static: true }) singleSelect: MatSelect;

    public userFilterCtrl: FormControl = new FormControl();
    public users: User[] = [];
    public user: User;
    public userCtrl: FormControl = new FormControl();
    public title = 'Add Owner';
    public status = [
      { id: 1, name: 'ACTIVE' },
      { id: 2, name: 'SUSPENDED' },
      { id: 3, name: 'DEACTIVATED' },
    ];

    public userTypes = [
      { id: 4, name: 'LandLord' },
      { id: 5, name: 'CareTaker' },
      { id: 6, name: 'Tenant' },
    ];

    public gender = [
      { id: 1, name: 'MALE' },
      { id: 2, name: 'FEMALE' },
      { id: 3, name: 'RATHER NOT SAY' },
    ];
    public type =  { id: 1, name: 'ACTIVE' };
    public infoForm: FormGroup;


  /** list of users filtered by search keyword */
  public filteredUsers: ReplaySubject<User[]> = new ReplaySubject<User[]>(1);

  /** Subject that emits when the component has been destroyed. */
  protected _onDestroy = new Subject<void>();

  public closeDialog() {
    this.dialogRef.close();
  }
  public onInfoFormSubmit(owner: Owner) {
    alert(JSON.stringify(owner));
    if (this.infoForm.valid ) {
      this.data = owner;
      this.dialogRef.close();
    }

  }

  ngOnInit(): void {
      // listen for search field value changes
      this.userFilterCtrl.valueChanges
      .pipe(takeUntil(this._onDestroy))
      .subscribe(() => {
        this.filterUsers();

      });

    this.infoForm = this.formBuilder.group({
      status: ['', Validators.required],
      userType: ['', Validators.required],
      phone: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      idNumber: [''],
      user: null,
      gender: [''],
      fullName: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      email: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      description: [''],

    });

    if (this.data && this.data._id) {
      this.title = 'Edit Owner ' +   this.data.user.fullName;
      this.infoForm.patchValue(this.data.user);
    }
 }

  protected filterUsers() {
    if (!this.users) {
      return;
    }
    // get the search keyword
    let search = this.userFilterCtrl.value;
    if (!search) {
      this.filteredUsers.next(this.users.slice());
      return;
    } else {
      search = search.toLowerCase();
    }

     this.ownderService.searchUsers(search).subscribe(data => {
      this.users = data.users;
     });

     this.userCtrl.valueChanges.subscribe(value => {
       if (value) {
         const searchedUser = value;
         searchedUser.userObj = value;
         this.data = {};
         this.user = value;
         this.data.user = value;
         this.infoForm.patchValue(searchedUser);

       } else {
         this.infoForm.reset();
       }
     });
  }


}

