
import { HunitAction, HunitActionType } from './hunit.actions';
import { initialHunitState, HunitState, hunitAdapter } from './hunit.state';


export function hunitReducer(state = initialHunitState, action: HunitAction): HunitState {
  switch (action.type) {
    case HunitActionType.Loading: {
      return { ...state, loading: true };
    }

    case HunitActionType.LoadSuccess: {

      if (action.payload.hunits){
         return hunitAdapter.addAll(action.payload.hunits.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.hunits.page,
        total: action.payload.hunits.totalDocs,
      });
      }else if (action.payload.hunit){
        return hunitAdapter.upsertOne(action.payload.hunit, state);
      }

    }
    case HunitActionType.DeleteLoading:
    case HunitActionType.UpdateLoading:
    case HunitActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case HunitActionType.UpdateSuccess:
    case HunitActionType.InsertSuccess: {
    return hunitAdapter.upsertOne(action.payload.hunit, state);
    }
    case HunitActionType.UpdateError:
    case HunitActionType.DeleteError:
    case HunitActionType.InsertError: {
      return { ...state, loading: false, error: true };
    }

    case HunitActionType.DeleteSuccess: {
      return hunitAdapter.removeOne(action.payload.hunit._id, state);
    }
    case HunitActionType.LoadFailure: {
      return hunitAdapter.removeAll({
        ...state,
        error: true,
        loading: false,
        total: 0,
      });
    }
    default:
      return state;
  }
}
