import { Component, OnInit, ViewChild, Inject, NgZone, ElementRef } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Pgroup } from '../models/pgroup';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { Store, select } from '@ngrx/store';
import { PgroupLoadAction, PgroupDeleteAction, PgroupInsertAction, PgroupUpdateAction } from '../store/pgroup.actions';
import { PgroupService } from '../services/pgroup.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PgroupParams } from '../models/pgroup.params';
import { selectAllPgroup, selectPgroupTotal } from '../store/pgroup.selectors';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppService } from './../../../app.service';
import { GlobalState } from './../../../store/states/global.state';
import { MapsAPILoader } from '@agm/core';


@Component({
  selector: 'app-pgroup',
  templateUrl: './pgroup.component.html',
  styleUrls: ['./pgroup.component.scss']
})
export class PgroupComponent implements OnInit {
  displayedColumns: string[] = ['title', 'address', 'status', 'actions' ];
  dataSource: MatTableDataSource<Pgroup>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public pgroup: Pgroup = {};
  private userId: string;


  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private pgroupService: PgroupService,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.store.pipe(select(selectAllPgroup)).subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });


    this.store.pipe(select(selectPgroupTotal)).subscribe(total => {
        this.total = total;
       });

    this.loadPgroup({page: 1, limit: 10});
    this.activatedRoute.params.subscribe(params => {
      this.userId = params.id;

    });

  }

 public openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
       duration: 2000,
    }).afterDismissed().subscribe(info => {
      if (info.dismissedByAction === true) {

      }
    });
 }

public loadPgroup(data){
   this.store.dispatch(new PgroupLoadAction(data));
  }

  public remove(pgroup: Pgroup) {
    this.snackBar.open('Are you sure you want to delete?', 'DELETE', {
      duration: 2000,
   }).afterDismissed().subscribe(info => {
     if (info.dismissedByAction === true) {
      this.store.dispatch(new PgroupDeleteAction(pgroup));
      const index: number = this.dataSource.data.indexOf(pgroup);
      if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Pgroup>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
     }});


  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

openEditDialog(pgroup: Pgroup): void {

  const dialogRef = this.dialog.open(AddPgroupComponent, {
    width: '500px',
    data: pgroup
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');

    this.pgroup = result;
    this.pgroup._id = pgroup._id;
    this.pgroup.userId = this.userId;
    const pgroupParams =  { pgroup: this.pgroup} as PgroupParams;
    this.store.dispatch(new PgroupUpdateAction(pgroupParams));

  });
}

openDialog(): void {
  const dialogRef = this.dialog.open(AddPgroupComponent, {
    width: '500px',
    data: this.pgroup
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    if (result){
      this.pgroup = result;
      this.pgroup.userId = this.userId;
      const pgroupParams =  { pgroup: this.pgroup} as PgroupParams;
      this.store.dispatch(new PgroupInsertAction(pgroupParams));
    }

  });
}

}

@Component({
  selector: 'app-add-pgroup',
  templateUrl: './add-pgroup.component.html',
  styleUrls: ['./add-pgroup.component.scss']
})
export class AddPgroupComponent implements OnInit {
  @ViewChild('addressAutocomplete') addressAutocomplete: ElementRef;
  constructor(
    public dialogRef: MatDialogRef<AddPgroupComponent>,
    public formBuilder: FormBuilder,
    private mapsAPILoader: MapsAPILoader,
    public store: Store<GlobalState>,
    private ngZone: NgZone,
    private appService: AppService,
    @Inject(MAT_DIALOG_DATA)
    public data: Pgroup) {

    }
    public lat = 40.678178;
    public lng = -73.944158;
    public zoom = 12;
    public title = 'Add Property';
    public infoForm: FormGroup;

  public closeDialog(){
    this.dialogRef.close();
  }
  public onInfoFormSubmit(pgroup: Pgroup){
    if (this.infoForm.valid){
      alert(JSON.stringify({lat: this.lat, lng: this.lng}));
      this.data = pgroup;
      this.data.location = {lat: this.lat, lng: this.lng};
      this.dialogRef.close();
    }
  }

  public getAddress(){
    this.appService.getAddress(this.lat, this.lng).subscribe(response => {
      const responseResult: any = response;
      console.log(response);
      if (responseResult.results.length){
        const address = responseResult.results[0].formatted_address;
        this.infoForm.controls.address.get('address').setValue(address);
      }
    });
  }


  private placesAutocomplete(){
    this.mapsAPILoader.load().then(() => {
      const autocomplete = new google.maps.places.Autocomplete(this.addressAutocomplete.nativeElement, {

      });
      autocomplete.addListener('place_changed', () => {
        this.ngZone.run(() => {
          const place: google.maps.places.PlaceResult = autocomplete.getPlace();
          if (place.geometry === undefined || place.geometry === null) {
            return;
          }
          this.lat = place.geometry.location.lat();
          this.lng = place.geometry.location.lng();
          this.getAddress();
        });
      });
    });
  }


  ngOnInit(): void {

    this.infoForm = this.formBuilder.group({
      description: ['', Validators.required],
      address: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      location:  null,
      title: ['', Validators.compose([Validators.required, Validators.minLength(3)])],

    });
    if (this.data && this.data._id){
      this.title = 'Edit Property ' +   this.data.title;
      this.infoForm.patchValue(this.data);
      if (this.data.location){
        this.lat = this.data.location.lat;
        this.lng = this.data.location.lng;
      }

    }
    this.placesAutocomplete();
  }

}

