import { Component, OnInit, Input, HostListener } from '@angular/core';
import { DomSanitizer} from '@angular/platform-browser';
import { Settings, AppSettings } from '../../app.settings';
import { MediaObserver, MediaChange } from '@angular/flex-layout';
import { Subscription } from 'rxjs';
import { Store, select } from '@ngrx/store';
import { selectAllPropertyDevelopment } from 'src/app/pages/properties/store/developments/property.developments.selectors';
import { GlobalState } from 'src/app/store/states/global.state';
import { Property } from 'src/app/pages/properties/models/property';
import { PropertyDevelopmentAction } from 'src/app/pages/properties/store/property.actions';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header-image',
  templateUrl: './header-image.component.html',
  styleUrls: ['./header-image.component.scss']
})
export class HeaderImageComponent implements OnInit {
  @Input('backgroundImage') backgroundImage: string[];
  @Input('bgImageAnimate') bgImageAnimate;
  @Input('contentOffsetToTop') contentOffsetToTop;
  @Input('contentMinHeight') contentMinHeight;
  @Input('title') title;
  @Input('desc') desc;
  @Input('titles') titles: any[];
  @Input('descriptions') descriptions: any[];
  @Input('isHomePage') isHomePage ;
  public bgImage;
  public settings: Settings;
  public index = 0;
  public expandedCaption = false;
  public currentTitle: string;
  public currentDescription: string;
  public showImaportantMessage = true;
  public innerWidth: any;
  public imageList = this.backgroundImage;
  public captionExpanded = true;
  public panelOpenState = false;
  public properties: Property[];
  public currentPropertyId = '';
  public propertyPrice = 'From 2m';
  public property: Property;
  public watcher: Subscription;
  constructor(public appSettings: AppSettings,
              private sanitizer: DomSanitizer,
              private mediaObserver: MediaObserver,
              private router: Router,
              private store: Store<GlobalState>
     ) {
    this.settings = this.appSettings.settings;
    this.settings.contentOffsetToTop = false;

    setTimeout(() => {
      this.settings.headerBgImage = !(window.innerWidth < 600);
    });

    this.watcher = mediaObserver.media$.subscribe((change: MediaChange) => {

      if (change.mqAlias == 'xs') {
        this.settings.headerBgImage = false;
      }
      else if (change.mqAlias == 'sm'){
        this.settings.headerBgImage = false;
      }

      else{
        this.settings.headerBgImage = true;

      }
    });

    this.store.pipe(select(selectAllPropertyDevelopment)).subscribe(properties => {
        this.properties = properties;
        if (properties && properties.length > 0){
          this.setProperty();
        }
    });

    this.store.dispatch(new PropertyDevelopmentAction({featured: true}));

  }

  public openProperty(){
    this.router.navigateByUrl('/properties/' + this.currentPropertyId );
  }

  public setProperty(){
    if (this.properties[this.index]){

      const property = this.properties[this.index];
      this.property = property;
      this.currentTitle = property.title;
      this.currentDescription  = property.shortDesc;
      this.currentPropertyId = property._id;
      if (property.propertyStatus && property.propertyStatus.includes('For Rent')){
        this.propertyPrice = `From Ksh. ${ this.formatMoney(property.priceDollar.rent)} pm` ;
      }

      if (property.propertyStatus && property.propertyStatus.includes('For Sale')){
        if ( property.priceDollar.sale > 1000000){
          this.propertyPrice = `From Ksh. ${this.formatMoney((property.priceDollar.sale / 1000000))} m`;
        }else{
          this.propertyPrice = `From Ksh. ${ this.formatMoney(property.priceDollar.sale)}` ;
        }

       }

    }
  }

public formatMoney(amount: number, decimalCount = 0, decimal = '.', thousands = ',') {
    try {
      decimalCount = Math.abs(decimalCount);
      decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

      const negativeSign = amount < 0 ? '-' : '';

      const i = parseInt(amount.toString());
      const j = (i.toString().length > 3) ? i.toString().length % 3 : 0;

      return negativeSign + (j ? i.toString().substr(0, j) + thousands : '')
      + i.toString().substr(j).replace(/(\d{3})(?=\d)/g, '$1' + thousands)
      + (decimalCount ? decimal + Math.abs(amount - i).toFixed(decimalCount).slice(2) : '');
    } catch (e) {
      console.log(e);
    }
  }
  ngOnInit() {
    this.innerWidth = window.innerWidth;
    if (this.contentOffsetToTop){
      setTimeout(() => {
          // this.settings.contentOffsetToTop = this.contentOffsetToTop;
      });
    }

    setTimeout(() => {
     // this.captionExpanded = false;
    }, 10000);

    this.imageList = this.backgroundImage;
    if (this.backgroundImage){
      this.bgImage = this.sanitizer.bypassSecurityTrustStyle('url(' + this.backgroundImage[0] + ')');
      this.setProperty();

    }



    setInterval(() => {
         if (this.index > this.properties.length - 1){
           this.index = 0;
         }

         this.setProperty();

         if (this.imageList[this.index]){
          this.bgImage = this.sanitizer.bypassSecurityTrustStyle('url(' + this.imageList[this.index] + ')');
         }

         this.index = this.index + 1;
         // this.captionExpanded = true;
         setTimeout(() => {
         // this.captionExpanded = false;
       }, 5000);

    }, 10000);
  }


  public  goToNextPhoto(){
    if (this.index > this.properties.length - 1){
      this.index = 0;
    }else{
      this.index = this.index + 1;
    }
    this.setProperty();

    if (this.imageList && this.imageList[this.index]){
      this.bgImage = this.sanitizer.bypassSecurityTrustStyle('url(' + this.imageList[this.index] + ')');
    }
  }

  public goToPreviousPhoto(){
    if (this.index < 1){
      this.index = this.properties.length - 1;
    }else{
      this.index = this.index - 1;
    }
    this.currentTitle = this.titles[this.index];
    this.currentDescription  = this.descriptions[this.index];

    if (this.imageList[this.index]){

     this.bgImage = this.sanitizer.bypassSecurityTrustStyle('url(' + this.imageList[this.index] + ')');

    }
  }


  @HostListener('window:resize', ['$event'])
onResize(event) {

  this.innerWidth = window.innerWidth;
  this.settings.headerBgImage = !(window.innerWidth < 399);
}

  public close(){
    this.showImaportantMessage = false;
  }
  public expandImageCaption(){
    if (this.expandedCaption){
      this.expandedCaption = false;
    }else{
      this.expandedCaption = true;
    }

  }


  ngOnDestroy(){
    this.watcher.unsubscribe();
    setTimeout(() => {
      this.settings.headerBgImage = false;
      this.settings.contentOffsetToTop = false;
    });
  }

}
