import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { UserParams } from '../models/user-params';
import { UserResponse } from '../models/user-response';
import { User } from '../models/user';
import { environment } from '../../../../environments/environment.prod';
import { toUser } from '../../properties/utils/to-formdata';

@Injectable()
export class UserService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getUsers(params: UserParams): Observable<UserResponse> {
    const page: String = (params.pageIndex + 1).toString();
    const limit: String = params.pageSize.toString();
    return this.httpClient.get<UserResponse>(`${this.url}/user?page=${page}&limit=${limit}`, {});
  }

  public createUser(params: User): Observable<UserResponse>{
    return this.httpClient.post<UserResponse>(`${this.url}/register`, params);
  }

  public loginUser(params: User): Observable<UserResponse>{
    return this.httpClient.post<UserResponse>(`${this.url}/login`, params);
  }

  public updateUser(params: UserParams): Observable<UserResponse> {
    const data = toUser(params.user, params.file);
    return this.httpClient.put<UserResponse>(`${this.url}/user`, data);
  }

  public deleteUser(params: User): Observable<UserResponse>{
    return this.httpClient.delete<UserResponse>(`${this.url}/user?_id=${params._id}`);
  }

}

