import { Component, OnInit, ViewChild, Inject, NgZone, ElementRef } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Feedback } from '../models/feedback';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { Store, select } from '@ngrx/store';
import { FeedbackLoadAction, FeedbackDeleteAction, FeedbackInsertAction, FeedbackUpdateAction } from '../store/feedback.actions';
import { FeedbackService } from '../services/feedback.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FeedbackParams } from '../models/feedback.params';
import { selectAllFeedback, selectFeedbackTotal } from '../store/feedback.selectors';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppService } from '../../../app.service';
import { GlobalState } from '../../../store/states/global.state';
import { MapsAPILoader } from '@agm/core';
import { PopupComponent } from '../../popup/popup.component';
import { Publication } from '../../publication/models/publication';


@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss']
})
export class FeedbackComponent implements OnInit {
  displayedColumns: string[] = ['name', 'email', 'phone', 'actions' ];
  dataSource: MatTableDataSource<Feedback>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public feedback: Feedback = {};
  private userId: string;


  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private feedbackService: FeedbackService,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.store.pipe(select(selectAllFeedback)).subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });


    this.store.pipe(select(selectFeedbackTotal)).subscribe(total => {
        this.total = total;
       });

    this.loadFeedback({page: 1, limit: 10});
    this.activatedRoute.params.subscribe(params => {
      this.userId = params.id;

    });

  }

 public openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
       duration: 2000,
    }).afterDismissed().subscribe(info => {
      if (info.dismissedByAction === true) {

      }
    });
 }

public loadFeedback(data){
   this.store.dispatch(new FeedbackLoadAction(data));
  }

  public remove(feedback: Feedback) {
    this.snackBar.open('Are you sure you want to delete?', 'DELETE', {
      duration: 2000,
   }).afterDismissed().subscribe(info => {
     if (info.dismissedByAction === true) {
      this.store.dispatch(new FeedbackDeleteAction(feedback));
      const index: number = this.dataSource.data.indexOf(feedback);
      if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Feedback>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
     }});


  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

openEditDialog(feedback: Feedback): void {

  const dialogRef = this.dialog.open(AddFeedbackComponent, {
    width: '500px',
    data: feedback
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');

    this.feedback = result;
    this.feedback._id = feedback._id;
    this.feedback.userId = this.userId;
    const feedbackParams =  { feedback: this.feedback} as FeedbackParams;
    this.store.dispatch(new FeedbackUpdateAction(feedbackParams));

  });
}
openDetails(feedback: Feedback){
  this.openDetailDialog({title: feedback.name, content: feedback.message
  });
}

openDetailDialog(detail: Publication): void {

  const dialogRef = this.dialog.open(PopupComponent, {
    width: '500px',
    data: detail
  });

  dialogRef.afterClosed().subscribe(result => {


  });
}

openDialog(): void {
  const dialogRef = this.dialog.open(AddFeedbackComponent, {
    width: '500px',
    data: this.feedback
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    if (result){
      this.feedback = result;
      this.feedback.userId = this.userId;
      const feedbackParams =  { feedback: this.feedback} as FeedbackParams;
      this.store.dispatch(new FeedbackInsertAction(feedbackParams));
    }

  });
}

}

@Component({
  selector: 'app-add-feedback',
  templateUrl: './add-feedback.component.html',
  styleUrls: ['./add-feedback.component.scss']
})
export class AddFeedbackComponent implements OnInit {
  @ViewChild('addressAutocomplete') addressAutocomplete: ElementRef;
  constructor(
    public dialogRef: MatDialogRef<AddFeedbackComponent>,
    public formBuilder: FormBuilder,
    private mapsAPILoader: MapsAPILoader,
    public store: Store<GlobalState>,
    private ngZone: NgZone,
    private appService: AppService,
    @Inject(MAT_DIALOG_DATA)
    public data: Feedback) {

    }
    public lat = 40.678178;
    public lng = -73.944158;
    public zoom = 12;
    public title = 'Add Property';
    public infoForm: FormGroup;

  public closeDialog(){
    this.dialogRef.close();
  }
  public onInfoFormSubmit(feedback: Feedback){
    if (this.infoForm.valid){
      this.data = feedback;
      this.dialogRef.close();
    }
  }

  public getAddress(){
    this.appService.getAddress(this.lat, this.lng).subscribe(response => {
      const responseResult: any = response;
      console.log(response);
      if (responseResult.results.length){
        const address = responseResult.results[0].formatted_address;
        this.infoForm.controls.address.get('address').setValue(address);
      }
    });
  }


  private placesAutocomplete(){
    this.mapsAPILoader.load().then(() => {
      const autocomplete = new google.maps.places.Autocomplete(this.addressAutocomplete.nativeElement, {

      });
      autocomplete.addListener('place_changed', () => {
        this.ngZone.run(() => {
          const place: google.maps.places.PlaceResult = autocomplete.getPlace();
          if (place.geometry === undefined || place.geometry === null) {
            return;
          }
          this.lat = place.geometry.location.lat();
          this.lng = place.geometry.location.lng();
          this.getAddress();
        });
      });
    });
  }


  ngOnInit(): void {

    this.infoForm = this.formBuilder.group({
      description: ['', Validators.required],
      address: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      location:  null,
      title: ['', Validators.compose([Validators.required, Validators.minLength(3)])],

    });
    if (this.data && this.data._id){
      this.title = 'Edit Property ' +   this.data.title;
      this.infoForm.patchValue(this.data);
    }
    this.placesAutocomplete();
  }

}

