import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { User } from '../models/user';
import { environment } from '../../../../environments/environment.prod';
import { Store } from '@ngrx/store';
import { GlobalState } from 'src/app/store/states/global.state';
import { UserLogoutAction } from '../store/user.actions';

const tokenName = 'token';

@Injectable({
  providedIn: 'root',
})
export class AuthService {

  private checkLogin: boolean = localStorage.getItem('user') != null;
  private isLogged$ = new BehaviorSubject(this.checkLogin);
  private url = `${environment.apiBaseUrl}/api`;
  private user: User =  {};

  constructor(private http: HttpClient, private store: Store<GlobalState>) {

  }

  public get isLoggedIn(): boolean {
    return this.isLogged$.value;
  }

  public saveUser(user: User, token: string) {
   localStorage.setItem('user', JSON.stringify(user));
   if (token){
    localStorage.setItem(tokenName, token);
   }
   this.isLogged$.next(true);
  }

  public logout() {
    localStorage.clear();
    this.user = null;
    this.isLogged$.next(false);
    this.store.dispatch(new UserLogoutAction());
  }

  public get authToken(): string {
    return localStorage.getItem(tokenName);
  }

  public saveAuthToken(token: string){
    localStorage.setItem(tokenName, token);
  }

  public get userData(): Observable<User> {
    return this.loadUser();
  }

  private loadUser(): Observable<User> {
    const serializedUser = localStorage.getItem('user');
    if (serializedUser) {
      this.user = JSON.parse(serializedUser) as User ;
    }
    return of(this.user);
  }
}
