import { createSelector, createFeatureSelector } from '@ngrx/store';
import { userAdapter, UserState } from './user.state';
import { User } from '../models/user';


export const {
  selectIds: _selectUserDataIds,
  selectEntities: _selectUserEntities,
  selectAll: _selectAllUser,
  selectTotal: _selectUserTotal,
} = userAdapter.getSelectors();

export const selectUserState = createFeatureSelector<UserState>('user');

export const selectUserIds = createSelector(
  selectUserState,
  _selectUserDataIds,
);

export const selectUserEntities = createSelector(
  selectUserState,
  _selectUserEntities,
);

export const selectAllUser = createSelector(
  selectUserState,
  _selectAllUser,
);

export const selectUserError = createSelector(
  selectUserState,
  (state: UserState): string => state.error,
);

export const selectUserLoading = createSelector(
  selectUserState,
  (state: UserState): boolean => state.loading,
);



export const selectUserTotal = createSelector(
  selectUserState,
  (state: UserState): number => state.total,
);

export const selectCurrentUser = createSelector(
  selectUserState,
  (state: UserState): User => state.user,
);

export const selectUserToken = createSelector(
  selectUserState,
  (state: UserState): string => state.token,
);
