import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { Property, Pagination } from 'src/app/app.models';
import { Settings, AppSettings } from 'src/app/app.settings';
import { AppService } from 'src/app/app.service';
import { MediaObserver, MediaChange } from '@angular/flex-layout';
import { Store, select } from '@ngrx/store';
import { GlobalState } from 'src/app/store/states/global.state';
import { debounceTime, distinctUntilChanged, distinct } from 'rxjs/operators';
import { PropertyLoadAction } from '../properties/store/property.actions';
import { ActivatedRoute } from '@angular/router';
import { selectAllPropertySearch } from '../properties/store/search/property.search.selectors';

@Component({
  selector: 'app-sales',
  templateUrl: './sales.component.html',
  styleUrls: ['./sales.component.scss']
})
export class SalesComponent implements OnInit {

  watcher: Subscription;
  activeMediaQuery = '';

  public slides = [];
  public properties: Property[] = [];
  public viewType = 'list';
  public viewCol = 33.3;
  public count = 8;
  public sort: string;
  public searchFields: any;
  public removedSearchField: string;
  public pagination: Pagination = new Pagination(1, 8, null, 2, 0, 0);
  public message: string;
  public featuredProperties: Property[];
  public showResult = false;
  public type = 'Rent';
  public title = '';
  public description = '';
  public desc = ['An unprecedented concept set to transform Riverside Drive, Riverside Square is a mixed use marvel that brings together shopping, working, living and leisure.', 'An unprecedented concept set to transform Riverside Drive, Riverside Square is a mixed use marvel that brings together shopping, working, living and leisure.', 'An unprecedented concept set to transform Riverside Drive, Riverside Square is a mixed use marvel that brings together shopping, working, living and leisure.'];
  public titles = ['Milimani', 'Cairo Heights', 'El capoor', 'Serene Gardens'];
  public settings: Settings;
  public content = [{
   title : 'Explore our Exciting Portfolio of Homes and Investment Properties',
     description : `Your new investment is just a search away. Search our real-time listings for properties ranging from individual homes and offices to large scale mixed use developments. Based on what you would like most, we have something in every price range and from all over the country!`
  }, {
    title: 'Discover the City’s Best Property Developments',
    description: 'Mapple Homes offers investment-grade developments all over the city. The developments we offer have been conceptualized by our development team to be high quality assets using a data-driven approach in planning and design, that gears property for high rental returns and an escalated rate of capital growth'
  },
  {
    title: 'Find a Place That is Perfect Just for You',
    description: 'Looking for a home or office to lease? Search from our carefully selected listings based on what matters most to you. Based on what you would like most, we have something in every price range and from all over the country!',
  }


  ];

  public propertyStatus = 'For Rent';
  public isSmallScreen = false;

  constructor(public appSettings: AppSettings,
              public appService: AppService,
              public mediaObserver: MediaObserver,
              private activatedRoute: ActivatedRoute,
              private store: Store<GlobalState>) {

    this.settings = this.appSettings.settings;
    this.watcher = mediaObserver.media$.subscribe((change: MediaChange) => {
      // console.log(change)
      if (change.mqAlias == 'xs') {
        this.viewCol = 100;
        this.viewType = 'grid';
        this.isSmallScreen = true;
      }
      else if (change.mqAlias == 'sm'){
        this.viewCol = 50;
        this.viewType = 'grid';
        this.isSmallScreen = true;
      }
      else if (change.mqAlias == 'md'){
        this.viewCol = 33.3;
        this.viewType = 'list';
        this.isSmallScreen = false;
      }
      else{
        this.viewCol = 33.3;
       // this.viewCol = 25;
        this.isSmallScreen = false;
      }
    });

  }

  ngOnInit() {
    this.getSlides();
    this.activatedRoute.params.subscribe(params => {
      if (params.id) {
        this.type =  params.id;

        if (this.type === 'rent'){
          this.setConent(this.content[2]);
          this.propertyStatus = 'For Rent';

        } else if (this.type === 'buy'){
          this.setConent(this.content[0]);
          this.propertyStatus = 'For Sale';

       } else if (this.type === 'developments'){
        this.setConent(this.content[1]);
        this.propertyStatus = 'Development';
       }
        this.appService.filterProperty(this.Addfilter(this.type), this.sort, this.pagination.page, this.pagination.perPage);
      }

    });


    this.store.pipe(select(selectAllPropertySearch)).pipe(distinct(property => property)).subscribe(data => {
    if (this.properties && this.properties.length > 0){
              this.settings.loadMore.page++;
              this.pagination.page = this.settings.loadMore.page;
            }

    const result = this.filterData2(data);
    if ( data.length === 0){
              this.properties.length = 0;
              this.pagination = new Pagination(1, this.count, null, 2, 0, 0);
              this.message = 'No Results Found';
              return false;
            }
    if (this.properties && this.properties.length > 0){
              this.properties = this.properties.concat(result.data);
            }
            else{
              this.properties = result.data;
            }
    this.pagination = result.pagination;
    this.message = null;

    if (this.properties.length === this.pagination.total){
              this.settings.loadMore.complete = true;
              this.settings.loadMore.result = this.properties.length;
            }
            else{
              this.settings.loadMore.complete = false;
            }
    this.properties = data;

          });

  }

  public setConent(content: any){
    this.title = content.title;
    this.description = content.description;
  }

  ngDoCheck(){
    if (this.settings.loadMore.load){
      this.settings.loadMore.load = false;
      // this.getProperties();
    }
  }

  ngOnDestroy(){
    this.resetLoadMore();
    this.watcher.unsubscribe();
  }

  public getSlides(){
    this.appService.getHomeCarouselSlides().subscribe(res => {
      this.slides = res;
    });
  }

   public filterData2(data){
    return this.appService.paginator(data,  this.pagination.page, this.pagination.perPage);
  }
  public getProperties(){
    if (this.searchFields) {
      this.appService.filterProperty(this.searchFields, this.sort, this.pagination.page, this.pagination.perPage);
    }

  }

  public Addfilter(type){
    const data: any = {};
    if (type === 'rent'){
        data.propertyStatus = [{id: 0, name: 'For Rent'}];
    }else if (type === 'buy'){
      data.propertyStatus = [{id: 1, name: 'For Sale'}];
    }else if (type === 'developments'){
      data.propertyStatus = [{id: 2, name: 'Development'}];
    }
    this.searchFields = data;
    return data;
  }

  public resetLoadMore(){
    this.settings.loadMore.complete = false;
    this.settings.loadMore.start = false;
    this.settings.loadMore.page = 1;
    this.pagination = new Pagination(1, this.count, null, null, this.pagination.total, this.pagination.totalPages);

  }

  public filterData(data){
    return this.appService.filterData(data, this.searchFields, this.sort, this.pagination.page, this.pagination.perPage);
  }

  public searchClicked(){
    this.properties.length = 0;
    this.getProperties();
  }

  public clearChanged(event){
    this.showResult = false;
  }

  public searchChanged(event){
    event.valueChanges.subscribe(() => {
      this.showResult = true;
      this.resetLoadMore();
      this.searchFields = event.value;
      setTimeout(() => {
        this.removedSearchField = null;
      });
      if (!this.settings.searchOnBtnClick){
        if (this.properties){
          this.properties.length = 0;
        }

      }
    });
    event.valueChanges.pipe(debounceTime(500), distinctUntilChanged()).subscribe(() => {
      if (!this.settings.searchOnBtnClick){
        this.getProperties();
      }
    });
  }
  public removeSearchField(field){
    this.message = null;
    this.removedSearchField = field;
  }

  // public loadProperties(data){
  //   this.store.dispatch(new PropertyLoadAction(data));
  // }

  public changeCount(count){
    this.count = count;
    this.resetLoadMore();
    this.properties.length = 0;
    this.getProperties();

  }

  public changeSorting(sort){
    this.sort = sort;
    this.resetLoadMore();
    this.properties.length = 0;
    this.getProperties();
  }
  public changeViewType(obj){
    // alert(JSON.stringify(obj));
    this.viewType = obj.viewType;
    this.viewCol = obj.viewCol;
  }

  public getFeaturedProperties(){
    this.appService.getFeaturedProperties().subscribe(properties => {
      this.featuredProperties = properties;
    });
  }

}
