import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Tenant } from '../models/tenant';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { Store, select } from '@ngrx/store';
import { TenantLoadAction, TenantDeleteAction, TenantInsertAction, TenantUpdateAction } from '../store/tenant.actions';
import { TenantService } from '../services/tenant.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TenantParams } from '../models/tenant.params';
import { Hunit } from '../../../pages/account/housingunit/models/hunit';
import { AppService } from 'src/app/app.service';
import { GlobalState } from 'src/app/store/states/global.state';
import { img } from 'src/app/pages/properties/utils/to-formdata';
import { selectAllTenant, selectTenantTotal } from '../store/tenant.selectors';
import { MatSnackBar } from '@angular/material/snack-bar';
import { PropertyLoadAction } from 'src/app/pages/properties/store/property.actions';
import { AddPaymentComponent } from '../../payment/ui/payment.component';
import { Payment } from '../../payment/models/payment';
import { PaymentParams } from '../../payment/models/payment.params';
import { PaymentInsertAction } from '../../payment/store/payment.actions';
import { User } from 'src/app/pages/register/models/user';


@Component({
  selector: 'app-tenant',
  templateUrl: './tenant.component.html',
  styleUrls: ['./tenant.component.scss']
})
export class TenantComponent implements OnInit {
  displayedColumns: string[] = ['id', 'fullName', 'phone', 'tenantNumber', 'status', 'actions' ];
  dataSource: MatTableDataSource<Tenant>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public tenant: Tenant = {};
  public payment: Payment = {};
  private hunitId: string;
  public hunit: Hunit = {};

  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private tenantService: TenantService,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.store.pipe(select(selectAllTenant)).subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });


    this.store.pipe(select(selectTenantTotal)).subscribe(total => {
        this.total = total;
       });


    this.activatedRoute.params.subscribe(params => {
      this.hunitId = params.id;
      this.loadTenant({page: 1, limit: 10, hunitId: params.id});
      this.appService.getPropertyById(params.id).subscribe(hunit => {
        if (hunit){
          this.hunit = hunit;
        }

      });
    });

  }

 public openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
       duration: 2000,
    }).afterDismissed().subscribe(info => {
      if (info.dismissedByAction === true) {

      }
    });
 }

public loadTenant(data){
   this.store.dispatch(new TenantLoadAction(data));
  }

  public remove(tenant: Tenant) {
    this.snackBar.open('Are you sure you want to delete?', 'DELETE', {
      duration: 2000,
   }).afterDismissed().subscribe(info => {
     if (info.dismissedByAction === true) {
      this.store.dispatch(new TenantDeleteAction(tenant));
      const index: number = this.dataSource.data.indexOf(tenant);
      if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Tenant>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
     }});
  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

public img(url: string){
  if (url){
    return img(url);
  }
  return '';

}


openEditDialog(tenant: Tenant): void {
  const dialogRef = this.dialog.open(AddTenantComponent, {
    width: '500px',
    data: tenant
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    const user: User  = result;
    this.tenant = {user, status: result.status, hunitId: this.hunitId};
    this.tenant._id = tenant._id;
    this.tenant.hunitId = this.hunitId;
    const tenantParams =  { tenant: this.tenant} as TenantParams;
    this.store.dispatch(new TenantUpdateAction(tenantParams));

  });
}

openPaymentDialog(tenant: Tenant): void {
  const dialogRef = this.dialog.open(AddPaymentComponent, {
    width: '500px',
    data: tenant
  });


}
openDialog(): void {
  const dialogRef = this.dialog.open(AddTenantComponent, {
    width: '500px',
    data: this.tenant
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    if (result){
      const user: User  = result;
      this.tenant = {user, status: result.status, hunitId: this.hunitId};
      const tenantParams =  { tenant: this.tenant} as TenantParams;
      this.store.dispatch(new TenantInsertAction(tenantParams));
    }

  });
}

}

@Component({
  selector: 'app-add-tenant',
  templateUrl: './add-tenant.component.html',
  styleUrls: ['./add-tenant.component.scss']
})
export class AddTenantComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<AddTenantComponent>,
    public formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA)
    public data: Tenant) {

    }
    public title = 'Add Tenant';
    public status = [
      { id: 1, name: 'ACTIVE' },
      { id: 2, name: 'SUSPENDED' },
      { id: 3, name: 'DEACTIVATED' },
    ];

    public gender = [
      { id: 1, name: 'MALE' },
      { id: 2, name: 'FEMALE' },
      { id: 3, name: 'RATHER NOT SAY' },
    ];
    public type =  { id: 1, name: 'ACTIVE' };
    public infoForm: FormGroup;

  public closeDialog(){
    this.dialogRef.close();
  }
  public onInfoFormSubmit(tenant: Tenant){
    if (this.infoForm.valid){
      this.data = tenant;
      this.dialogRef.close();
    }

  }

  ngOnInit(): void {

    this.infoForm = this.formBuilder.group({
      status: ['', Validators.required],
      phone: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      idNumber: [''],
      gender: [''],
      fullName: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      email: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      description: [''],

    });
    if (this.data && this.data._id){
      this.title = 'Edit Tenant ' +   this.data.user.fullName;
      this.infoForm.patchValue(this.data.user);
    }

  }

}

