
import { BillAction, BillActionType } from './bill.actions';
import { initialBillState, BillState, billAdapter } from './bill.state';


export function billReducer(state = initialBillState, action: BillAction): BillState {
  switch (action.type) {
    case BillActionType.Loading: {
      return { ...state, loading: true };
    }

    case BillActionType.LoadSuccess: {

      return billAdapter.addAll(action.payload.bills.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.bills.page,
        total: action.payload.bills.totalDocs,
      });
    }
    case BillActionType.DeleteLoading:
    case BillActionType.UpdateLoading:
    case BillActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case BillActionType.UpdateSuccess:
    case BillActionType.InsertSuccess: {
    return billAdapter.upsertOne(action.payload.bill, state);
    }
    case BillActionType.UpdateError:
    case BillActionType.DeleteError:
    case BillActionType.InsertError: {
      return { ...state, loading: false, error: true };
    }

    case BillActionType.DeleteSuccess: {
      return billAdapter.removeOne(action.payload.bill._id, state);
    }
    case BillActionType.LoadFailure: {
      return billAdapter.removeAll({
        ...state,
        error: true,
        loading: false,
        total: 0,
      });
    }
    default:
      return state;
  }
}
