import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { FeedbackParams } from '../models/feedback.params';
import { FeedbackResponse } from '../models/feedback.response';
import { Feedback } from '../models/feedback';
import { environment } from 'src/environments/environment.prod';


@Injectable()
export class FeedbackService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getFeedbacks(params: FeedbackParams): Observable<FeedbackResponse> {
    let page = '1';
    let limit = '10';
    let _idString = '';
    if (params.pageIndex){
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page){
       page = params.page.toString();
       limit = params.limit.toString();
    }
    if (params._id){
      _idString = `&_id=${params._id}`;
    }


    return this.httpClient.get<FeedbackResponse>(`${this.url}/feedback?page=${page}&limit=${limit}${_idString}`, {});
  }

  public createFeedback(params: FeedbackParams): Observable<FeedbackResponse>{
    return this.httpClient.post<FeedbackResponse>(`${this.url}/feedback`, (params.feedback));
  }

  public updateFeedback(params: FeedbackParams): Observable<FeedbackResponse> {
    return this.httpClient.put<FeedbackResponse>(`${this.url}/feedback`, params.feedback);
  }

  public deleteFeedback(params: Feedback): Observable<FeedbackResponse>{
    return this.httpClient.delete<FeedbackResponse>(`${this.url}/feedback?_id=${params._id}`);
  }

}

