import { Component, OnInit, Input } from '@angular/core';
import { MenuService } from '../menu.service';
import { Menu } from '../menu.model';
import { AuthService } from 'src/app/pages/register/services/auth.service';
import { Store, select } from '@ngrx/store';
import { GlobalState } from 'src/app/store/states/global.state';
import { selectCurrentUser } from 'src/app/pages/register/store/user.selectors';
import { User } from 'src/app/pages/register/models/user';

@Component({
  selector: 'app-vertical-menu',
  templateUrl: './vertical-menu.component.html',
  styleUrls: ['./vertical-menu.component.scss'],
  providers: [ MenuService ]
})
export class VerticalMenuComponent implements OnInit {
  @Input('menuParentId') menuParentId;
  public menuItems: Array<Menu>;
  constructor(public menuService: MenuService, private authService: AuthService, private store: Store<GlobalState>) { }

  ngOnInit() {
    this.menuItems = this.menuService.getVerticalMenuItems();
    if (this.authService.isLoggedIn){
      this.menuItems = this.menuItems.filter(item => !(item.title == 'My account'));
      this.menuItems.push(new Menu (60, 'My account', '/account/property-groups', null, null, false, 0) );
      this.menuItems = this.menuItems.filter(item => !(item.title == 'Login'));

   }else{
    this.menuItems = this.menuItems.filter(item => !(item.title == 'Login'));
    this.menuItems.push(new Menu (60, 'Login', '/login', null, null, false, 0));
    this.menuItems = this.menuItems.filter(item => !(item.title == 'My account'));
   }
    this.menuItems = this.menuItems.filter(item => item.parentId == this.menuParentId);
    this.store.pipe(select(selectCurrentUser)).subscribe(user => this.checkUsers(user) );
  }

  private checkUsers(user: User){
    if (user){
      this.menuItems = this.menuItems.filter(item => !(item.title == 'My account'));
      this.menuItems.push(new Menu (60, 'My account',  '/account/property-groups', null, null, false, 0) );
      this.menuItems = this.menuItems.filter(item => !(item.title == 'Login'));
    }else{
      this.menuItems = this.menuItems.filter(item => !(item.title == 'Login'));
      this.menuItems.push(new Menu (60, 'Login', '/login', null, null, false, 0));
      this.menuItems = this.menuItems.filter(item => !(item.title == 'My account'));
    }
  }
  onClick(menuId){
    this.menuService.toggleMenuItem(menuId);
    this.menuService.closeOtherSubMenus(this.menuService.getVerticalMenuItems(), menuId);
  }

}
