
import { PaymentAction, PaymentActionType } from './payment.actions';
import { initialPaymentState, PaymentState, paymentAdapter } from './payment.state';


export function paymentReducer(state = initialPaymentState, action: PaymentAction): PaymentState {
  switch (action.type) {
    case PaymentActionType.Loading: {
      return { ...state, loading: true };
    }

    case PaymentActionType.LoadSuccess: {
  return paymentAdapter.setAll(action.payload.payments.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.payments.page,
        total: action.payload.payments.totalDocs,
      });
    }
    case PaymentActionType.DeleteLoading:
    case PaymentActionType.UpdateLoading:
    case PaymentActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case PaymentActionType.UpdateSuccess:
    case PaymentActionType.InsertSuccess: {
    return paymentAdapter.upsertOne(action.payload.payment, state);
    }
    case PaymentActionType.UpdateError:
    case PaymentActionType.DeleteError:
    case PaymentActionType.InsertError: {
      return { ...state, loading: false, error: true };
    }

    case PaymentActionType.DeleteSuccess: {
      return paymentAdapter.removeOne(action.payload.payment._id, state);
    }
    case PaymentActionType.LoadFailure: {
      return paymentAdapter.removeAll({
        ...state,
        error: true,
        loading: false,
        total: 0,
      });
    }
    default:
      return state;
  }
}
