import { Action } from '@ngrx/store';
import { OwnerParams } from '../models/owner.params';
import { OwnerResponse } from '../models/owner.response';
import { Owner } from '../models/owner';



export enum OwnerActionType {
  Loading = '[Owner] Loading',
  LoadSuccess = '[Owner] LoadSuccess',
  LoadFailure = '[Owner] LoadFailure',

  InsertSuccess = '[Owner] InsertSuccess',
  InsertLoading = '[Owner] InsertLoading',
  InsertError = '[Owner] InsertError',
  Insert = '[Owner] InsertOwner',

  UpdateSuccess = '[Owner] UpdateSuccess',
  UpdateLoading = '[Owner] UpdateLoading',
  UpdateError = '[Owner] UpdateError',
  Update = '[Owner] UpdateOwner',

  DeleteSuccess = '[Owner] DeleteSuccess',
  DeleteLoading = '[Owner] DeleteLoading',
  DeleteError = '[Owner] DeleteError',
  Delete = '[Owner] DeleteOwner',

}

export class OwnerLoadAction implements Action {
  public readonly type = OwnerActionType.Loading;

  constructor(public payload: OwnerParams) {
  }
}

export class OwnerInsertAction implements Action {
  public readonly type = OwnerActionType.Insert;
  constructor(public payload: OwnerParams) {}
}

export class OwnerInsertLoadingAction implements Action {
  public readonly type = OwnerActionType.InsertLoading;
  constructor(public payload: OwnerParams) {}
}

export class OwnerInsertSuccessAction implements Action {
  public readonly  type = OwnerActionType.InsertSuccess;
  constructor(public payload: OwnerResponse){}
 }

export class OwnerInsertErrorAction implements Action{
  public readonly type = OwnerActionType.InsertError;
  constructor(public error: any){}
}

export class OwnerLoadSuccessAction implements Action {
  public readonly type = OwnerActionType.LoadSuccess;
  constructor(public payload: OwnerResponse) {

  }
}


export class OwnerLoadFailAction implements Action {
  public readonly type = OwnerActionType.LoadFailure;
  constructor(public error: any) {
  }
}

// update
export class OwnerUpdateAction implements Action {
  public readonly type = OwnerActionType.Update;
  constructor(public payload: OwnerParams) {}
}



export class OwnerUpdateLoadingAction implements Action {
  public readonly type = OwnerActionType.UpdateLoading;
  constructor(public payload: OwnerParams) {}
}

export class OwnerUpdateSuccessAction implements Action {
  public readonly  type = OwnerActionType.UpdateSuccess;
  constructor(public payload: OwnerResponse){}
 }

export class OwnerUpdateErrorAction implements Action{
  public readonly type = OwnerActionType.UpdateError;
  constructor(public error: any){}
}

// update
export class OwnerDeleteAction implements Action {
  public readonly type = OwnerActionType.Delete;
  constructor(public payload: Owner) {}
}

export class OwnerDeleteLoadingAction implements Action {
  public readonly type = OwnerActionType.DeleteLoading;
  constructor(public payload: OwnerParams) {}
}

export class OwnerDeleteSuccessAction implements Action {
  public readonly  type = OwnerActionType.DeleteSuccess;
  constructor(public payload: OwnerResponse){}
 }

export class OwnerDeleteErrorAction implements Action{
  public readonly type = OwnerActionType.DeleteError;
  constructor(public error: any){}
}




export type OwnerAction = OwnerLoadAction
 | OwnerLoadSuccessAction
 | OwnerLoadFailAction

 |OwnerInsertSuccessAction
 |OwnerInsertErrorAction
 |OwnerInsertLoadingAction
 |OwnerInsertAction

 |OwnerUpdateSuccessAction
 |OwnerUpdateErrorAction
 |OwnerUpdateLoadingAction
 |OwnerUpdateAction

 |OwnerDeleteSuccessAction
 |OwnerDeleteErrorAction
 |OwnerDeleteLoadingAction
 |OwnerDeleteAction;
