import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { PaymentParams } from '../models/payment.params';
import { PaymentResponse } from '../models/payment.response';
import { Payment } from '../models/payment';
import { environment } from 'src/environments/environment.prod';



@Injectable()
export class PaymentService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getPayments(params: PaymentParams): Observable<PaymentResponse> {
    let page = '1';
    let limit = '10';
    let parent = '';
    if (params.pageIndex) {
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page) {
       page = params.page.toString();
       limit = params.limit.toString();
    }

    if (params.hunitId) {
      parent = `&hunit=${params.hunitId}`;
    }

    return this.httpClient.get<PaymentResponse>(`${this.url}/payment?page=${page}&limit=${limit}${parent}`, {});
  }

  public createPayment(params: PaymentParams): Observable<PaymentResponse> {
    return this.httpClient.post<PaymentResponse>(`${this.url}/payment`, (params.payment));
  }

  public updatePayment(params: PaymentParams): Observable<PaymentResponse> {
    return this.httpClient.put<PaymentResponse>(`${this.url}/payment`, params.payment);
  }

  public deletePayment(params: Payment): Observable<PaymentResponse> {
    return this.httpClient.delete<PaymentResponse>(`${this.url}/payment?_id=${params._id}`);
  }

}

