
import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { DomSanitizer} from '@angular/platform-browser';
import { Settings, AppSettings } from '../../app.settings';
import { Validators, FormBuilder, FormGroup, FormGroupDirective } from '@angular/forms';
import { emailValidator } from 'src/app/theme/utils/app-validators';
import { Contact } from 'src/app/pages/contact/model/contact';
import { NotificationService } from 'src/app/services/notification.service';
import { ContactService } from 'src/app/pages/contact/services/contact.service';

@Component({
  selector: 'app-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.scss']
})
export class BannerComponent implements OnInit {
  @ViewChild(FormGroupDirective) formGroupDirective: FormGroupDirective;
  @Input('backgroundImage') backgroundImage;
  @Input('backgroundImage2') backgroundImage2;
  @Input('backgroundImage3') backgroundImage3;
  @Input('backgroundImage4') backgroundImage4;
  @Input('bgImageAnimate') bgImageAnimate;
  @Input('contentOffsetToTop') contentOffsetToTop;
  @Input('contentMinHeight') contentMinHeight;
  @Input('title') title;
  @Input('desc') desc;
  @Input('isHomePage') isHomePage ;
  public bgImage;
  public settings: Settings;
  public index = 0;
  public expandedCaption = false;
  public currentTitle: string;
  public currentDescription: string;
  public contactForm: FormGroup;

  constructor(public appSettings: AppSettings,
              private sanitizer: DomSanitizer,
              private notificationService: NotificationService,
              private contactService: ContactService,
              public fb: FormBuilder) {
    this.settings = this.appSettings.settings;
    setTimeout(() => {
      // this.settings.headerBgImage = true;
    });
  }

  ngOnInit() {
    if (this.contentOffsetToTop){
      setTimeout(() => {
        this.settings.contentOffsetToTop = this.contentOffsetToTop;
      });
    }
    if (this.backgroundImage){
      this.bgImage = this.sanitizer.bypassSecurityTrustStyle('url(' + this.backgroundImage + ')');
    }

    this.contactForm = this.fb.group({
      name: ['', Validators.required],
      email: ['', Validators.compose([Validators.required, emailValidator])],
      phone: ['', Validators.required],
      message: ['', Validators.required]
    });

  }

  public onContactFormSubmit(values: Object){
    if (this.contactForm.valid) {
     const contact: Contact = values as Contact;
     this.contactService.addToMailingList(contact.email);
     this.contactService.createFeedback(contact).subscribe(
      (val) => {
         this.notificationService.openSnackBar('Thank your email has been recieved we will get back to you within 24hrs', 'close');
         setTimeout(() =>
         this.formGroupDirective.resetForm(), 0);
      },
      response => {
        if (this.contactForm.valid){
          this.notificationService.openSnackBar('Error could not saved', 'close');
        }

      },
      () => {
          console.log('The PATCH observable is now completed.');
      });


    }
  }

  ngOnDestroy(){
    setTimeout(() => {
      this.settings.headerBgImage = false;
      this.settings.contentOffsetToTop = false;
    });
  }

}
