import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Email } from '../models/email';




export interface EmailState extends EntityState<Email> {
  error: boolean;
  loading: boolean;
  total: number;
  page: number;
}

export const emailAdapter: EntityAdapter<Email> = createEntityAdapter<Email>({
  selectId: (email: Email) => email._id,
});

export const initialEmailState: EmailState = emailAdapter.getInitialState({
  error: false,
  loading: true,
  total: 0,
  page: 0,
});
