
import { FeedbackAction, FeedbackActionType } from './feedback.actions';
import { initialFeedbackState, FeedbackState, feedbackAdapter } from './feedback.state';


export function feedbackReducer(state = initialFeedbackState, action: FeedbackAction): FeedbackState {
  switch (action.type) {
    case FeedbackActionType.Loading: {
      return { ...state, loading: true };
    }

    case FeedbackActionType.LoadSuccess: {
      if (action.payload.feedback){
        return feedbackAdapter.upsertOne(action.payload.feedback, state);
      }

      return feedbackAdapter.setAll(action.payload.feedbacks.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.feedbacks.page,
        total: action.payload.feedbacks.totalDocs,
      });
    }
    case FeedbackActionType.DeleteLoading:
    case FeedbackActionType.UpdateLoading:
    case FeedbackActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case FeedbackActionType.UpdateSuccess:
    case FeedbackActionType.InsertSuccess: {
    return feedbackAdapter.upsertOne(action.payload.feedback, state);
    }
    case FeedbackActionType.UpdateError:
    case FeedbackActionType.DeleteError:
    case FeedbackActionType.InsertError: {
      return { ...state, loading: false, error: true };
    }

    case FeedbackActionType.DeleteSuccess: {
      return feedbackAdapter.removeOne(action.payload.feedback._id, state);
    }
    case FeedbackActionType.LoadFailure: {
      return feedbackAdapter.removeAll({
        ...state,
        error: true,
        loading: false,
        total: 0,
      });
    }
    default:
      return state;
  }
}
