import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Hunit } from '../models/hunit';




export interface HunitState extends EntityState<Hunit> {
  error: boolean;
  loading: boolean;
  total: number;
  page: number;
}

export const hunitAdapter: EntityAdapter<Hunit> = createEntityAdapter<Hunit>({
  selectId: (hunit: Hunit) => hunit._id,
});

export const initialHunitState: HunitState = hunitAdapter.getInitialState({
  error: false,
  loading: true,
  total: 0,
  page: 0,
});
