
import { EmailAction, EmailActionType } from './email.actions';
import { initialEmailState, EmailState, emailAdapter } from './email.state';


export function emailReducer(state = initialEmailState, action: EmailAction): EmailState {
  switch (action.type) {
    case EmailActionType.Loading: {
      return { ...state, loading: true };
    }

    case EmailActionType.LoadSuccess: {
      if (action.payload.email){
        return emailAdapter.upsertOne(action.payload.email, state);
      }

      return emailAdapter.setAll(action.payload.emails.docs, {
        ...state,
        error: false,
        loading: false,
        page: action.payload.emails.page,
        total: action.payload.emails.totalDocs,
      });
    }
    case EmailActionType.DeleteLoading:
    case EmailActionType.UpdateLoading:
    case EmailActionType.InsertLoading: {
      return { ...state, loading: true };
    }
    case EmailActionType.UpdateSuccess:
    case EmailActionType.InsertSuccess: {
    return emailAdapter.upsertOne(action.payload.email, state);
    }
    case EmailActionType.UpdateError:
    case EmailActionType.DeleteError:
    case EmailActionType.InsertError: {
      return { ...state, loading: false, error: true };
    }

    case EmailActionType.DeleteSuccess: {
      return emailAdapter.removeOne(action.payload.email._id, state);
    }
    case EmailActionType.LoadFailure: {
      return emailAdapter.removeAll({
        ...state,
        error: true,
        loading: false,
        total: 0,
      });
    }
    default:
      return state;
  }
}
