import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment.prod';
import { User } from '../../register/models/user';
import { Observable } from 'rxjs';
import { UserResponse } from '../../register/models/user-response';
import { HttpClient } from '@angular/common/http';
import { Contact } from './../model/contact';
import { NotificationService } from 'src/app/services/notification.service';

@Injectable({
  providedIn: 'root'
})
export class ContactService {

  constructor(private httpClient: HttpClient, private  notificationService: NotificationService) {  }

    private url = `${environment.apiBaseUrl}/api`;

    public createFeedback(params: Contact): Observable<any>{
       return this.httpClient.post<UserResponse>(`${this.url}/feedback`, params);
    }

  public addToMailingList(email: string): Observable<any>{
    const mail = {email};
    return this.httpClient.post<UserResponse>(`${this.url}/email`, mail);
 }


}
