import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { OwnerParams } from '../models/owner.params';
import { OwnerResponse } from '../models/owner.response';
import { Owner } from '../models/owner';
import { environment } from 'src/environments/environment.prod';
import { UserResponse } from 'src/app/pages/register/models/user-response';
import { UserPayload } from 'src/app/pages/register/models/user-payload';


@Injectable()
export class OwnerService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getOwners(params: OwnerParams): Observable<OwnerResponse> {
    let page = '1';
    let limit = '10';
    let parent = '';
    if (params.pageIndex) {
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page) {
       page = params.page.toString();
       limit = params.limit.toString();
    }

    if (params.hunitId) {
      parent = `&hunit=${params.hunitId}`;
    }

    return this.httpClient.get<OwnerResponse>(`${this.url}/owner?page=${page}&limit=${limit}${parent}`, {});
  }

  public searchUsers(query: string): Observable<UserPayload> {
    return this.httpClient.get<UserPayload>(`${this.url}/user?q=${query}`, {});
  }

  public createOwner(params: OwnerParams): Observable<OwnerResponse> {
    return this.httpClient.post<OwnerResponse>(`${this.url}/owner`, (params.owner));
  }

  public updateOwner(params: OwnerParams): Observable<OwnerResponse> {
    return this.httpClient.put<OwnerResponse>(`${this.url}/owner`, params.owner);
  }

  public deleteOwner(params: Owner): Observable<OwnerResponse> {
    return this.httpClient.delete<OwnerResponse>(`${this.url}/owner?_id=${params._id}`);
  }

}

