import { createSelector, createFeatureSelector } from '@ngrx/store';
import { propertyAdapter, PropertyState } from './property.state';


export const {
  selectIds: _selectPropertyDataIds,
  selectEntities: _selectPropertyEntities,
  selectAll: _selectAllProperty,
  selectTotal: _selectPropertyTotal,
} = propertyAdapter.getSelectors();

export const selectPropertyState = createFeatureSelector<PropertyState>('property');

export const selectPropertyIds = createSelector(
  selectPropertyState,
  _selectPropertyDataIds,
);

export const selectPropertyEntities = createSelector(
  selectPropertyState,
  _selectPropertyEntities,
);

export const selectAllProperty = createSelector(
  selectPropertyState,
  _selectAllProperty,
);

export const selectPropertyError = createSelector(
  selectPropertyState,
  (state: PropertyState): boolean => state.error,
);

export const selectPropertyLoading = createSelector(
  selectPropertyState,
  (state: PropertyState): boolean => state.loading,
);

export const selectPropertyTotal = createSelector(
  selectPropertyState,
  (state: PropertyState): number => state.total,
);
