
import { createSelector, createFeatureSelector } from '@ngrx/store';
import { emailAdapter, EmailState } from './email.state';


export const {
  selectIds: _selectEmailDataIds,
  selectEntities: _selectEmailEntities,
  selectAll: _selectAllEmail,
  selectTotal: _selectEmailTotal,
} = emailAdapter.getSelectors();

export const selectEmailState = createFeatureSelector<EmailState>('email');

export const selectEmailIds = createSelector(
  selectEmailState,
  _selectEmailDataIds,
);

export const selectEmailEntities = createSelector(
  selectEmailState,
  _selectEmailEntities,
);

export const selectAllEmail = createSelector(
  selectEmailState,
  _selectAllEmail,
);

export const selectEmailError = createSelector(
  selectEmailState,
  (state: EmailState): boolean => state.error,
);

export const selectEmailLoading = createSelector(
  selectEmailState,
  (state: EmailState): boolean => state.loading,
);

export const selectEmailTotal = createSelector(
  selectEmailState,
  (state: EmailState): number => state.total,
);
