import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { PublicationParams } from '../models/publication.params';
import { PublicationResponse } from '../models/publication.response';
import { Publication } from '../models/publication';
import { environment } from 'src/environments/environment.prod';
import { toFormData } from 'src/app/pages/properties/utils/to-formdata';


@Injectable()
export class PublicationService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getPublications(params: PublicationParams): Observable<PublicationResponse> {
    let page = '1';
    let limit = '10';
    let _idString = '';
    if (params.pageIndex){
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page){
       page = params.page.toString();
       limit = params.limit.toString();
    }
    if (params._id){
      _idString = `&_id=${params._id}`;
    }
    const param = {page, limit};


    return this.httpClient.get<PublicationResponse>(`${this.url}/publication`, {params: param});
  }

  public createPublication(params: PublicationParams): Observable<PublicationResponse>{
    return this.httpClient.post<PublicationResponse>(`${this.url}/publication`, (toFormData(params.publication)));
  }

  public updatePublication(params: PublicationParams): Observable<PublicationResponse> {
    return this.httpClient.put<PublicationResponse>(`${this.url}/publication`, params.publication);
  }

  public deletePublication(params: Publication): Observable<PublicationResponse>{
    return this.httpClient.delete<PublicationResponse>(`${this.url}/publication?_id=${params._id}`);
  }

}

