import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { EmailParams } from '../models/email.params';
import { EmailResponse } from '../models/email.response';
import { Email } from '../models/email';
import { environment } from 'src/environments/environment.prod';
import { toFormData } from 'src/app/pages/properties/utils/to-formdata';


@Injectable()
export class EmailService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getEmails(params: EmailParams): Observable<EmailResponse> {
    let page = '1';
    let limit = '10';
    let _idString = '';
    if (params.pageIndex){
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page){
       page = params.page.toString();
       limit = params.limit.toString();
    }
    if (params._id){
      _idString = `&_id=${params._id}`;
    }
    const param = {page, limit};


    return this.httpClient.get<EmailResponse>(`${this.url}/email`, {params: param});
  }

  public createEmail(params: EmailParams): Observable<EmailResponse>{
    return this.httpClient.post<EmailResponse>(`${this.url}/email`, (toFormData(params.email)));
  }

  public updateEmail(params: EmailParams): Observable<EmailResponse> {
    return this.httpClient.put<EmailResponse>(`${this.url}/email`, params.email);
  }

  public deleteEmail(params: Email): Observable<EmailResponse>{
    return this.httpClient.delete<EmailResponse>(`${this.url}/email?_id=${params._id}`);
  }

}

