import { createSelector, createFeatureSelector } from '@ngrx/store';

import { PropertyDevelopmentState, propertyAdapter } from './property.developments.state';


export const {
  selectIds: _selectPropertyDevelopmentDataIds,
  selectEntities: _selectPropertyDevelopmentEntities,
  selectAll: _selectAllPropertyDevelopment,
  selectTotal: _selectPropertyDevelopmentTotal,
} = propertyAdapter.getSelectors();

export const selectPropertyDevelopmentDevelopmentState = createFeatureSelector<PropertyDevelopmentState>('propertyDevelopment');

export const selectPropertyDevelopmentIds = createSelector(
  selectPropertyDevelopmentDevelopmentState,
  _selectPropertyDevelopmentDataIds,
);

export const selectPropertyDevelopmentEntities = createSelector(
  selectPropertyDevelopmentDevelopmentState,
  _selectPropertyDevelopmentEntities,
);

export const selectAllPropertyDevelopment = createSelector(
  selectPropertyDevelopmentDevelopmentState,
  _selectAllPropertyDevelopment,
);

export const selectPropertyDevelopmentError = createSelector(
  selectPropertyDevelopmentDevelopmentState,
  (state: PropertyDevelopmentState): boolean => state.error,
);

export const selectPropertyDevelopmentLoading = createSelector(
  selectPropertyDevelopmentDevelopmentState,
  (state: PropertyDevelopmentState): boolean => state.loading,
);

export const selectPropertyDevelopmentTotal = createSelector(
  selectPropertyDevelopmentDevelopmentState,
  (state: PropertyDevelopmentState): number => state.total,
);
