import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Payment } from '../models/payment';

export interface PaymentState extends EntityState<Payment> {
  error: boolean;
  loading: boolean;
  total: number;
  page: number;
}

export const paymentAdapter: EntityAdapter<Payment> = createEntityAdapter<Payment>({
  selectId: (payment: Payment) => payment._id,
});

export const initialPaymentState: PaymentState = paymentAdapter.getInitialState({
  error: false,
  loading: true,
  total: 0,
  page: 0,
});
