import { Action } from '@ngrx/store';
import { UserParams } from '../models/user-params';
import { UserResponse } from '../models/user-response';
import { User } from '../models/user';



export enum UserActionType {
  Loading = '[User] Loading',
  LoadSuccess = '[User] LoadSuccess',
  LoadFailure = '[User] LoadFailure',
  LogOut = '[User] LogOut',


  InsertSuccess = '[User] InsertSuccess',
  InsertLoading = '[User] InsertLoading',
  InsertError = '[User] InsertError',
  Insert = '[User] InsertUser',

  Login  = '[User] LoginUser',

  UpdateSuccess = '[User] UpdateSuccess',
  UpdateLoading = '[User] UpdateLoading',
  UpdateError = '[User] UpdateError',
  Update = '[User] UpdateUser',

  DeleteSuccess = '[User] DeleteSuccess',
  DeleteLoading = '[User] DeleteLoading',
  DeleteError = '[User] DeleteError',
  Delete = '[User] DeleteUser',

}

export class UserLoadAction implements Action {
  public readonly type = UserActionType.Loading;
  constructor(public payload: UserParams) {}
}

export class UserLogoutAction implements Action {
  public readonly type = UserActionType.LogOut;
  constructor() {}
}


export class UserInsertAction implements Action {
  public readonly type = UserActionType.Insert;
  constructor(public payload: User) {}
}

export class UserLoginAction implements Action {
  public readonly type = UserActionType.Login;
  constructor(public payload: User) {}
}

export class UserInsertLoadingAction implements Action {
  public readonly type = UserActionType.InsertLoading;
  constructor(public payload: UserParams) {}
}

export class UserInsertSuccessAction implements Action {
  public readonly  type = UserActionType.InsertSuccess;
  constructor(public payload: UserResponse){}
 }

export class UserInsertErrorAction implements Action{
  public readonly type = UserActionType.InsertError;
  constructor(public error: any){}
}

export class UserLoadSuccessAction implements Action {
  public readonly type = UserActionType.LoadSuccess;
  constructor(public payload: UserResponse) {}
}


export class UserLoadFailAction implements Action {
  public readonly type = UserActionType.LoadFailure;
  constructor(public error: any) {}
}

// update
export class UserUpdateAction implements Action {
  public readonly type = UserActionType.Update;
  constructor(public payload: UserParams) {}
}

export class UserUpdateLoadingAction implements Action {
  public readonly type = UserActionType.UpdateLoading;
  constructor(public payload: UserParams) {}
}

export class UserUpdateSuccessAction implements Action {
  public readonly  type = UserActionType.UpdateSuccess;
  constructor(public payload: UserResponse){}
 }

export class UserUpdateErrorAction implements Action{
  public readonly type = UserActionType.UpdateError;
  constructor(public error: any){}
}

// update
export class UserDeleteAction implements Action {
  public readonly type = UserActionType.Delete;
  constructor(public payload: UserParams) {}
}

export class UserDeleteLoadingAction implements Action {
  public readonly type = UserActionType.DeleteLoading;
  constructor(public payload: UserParams) {}
}

export class UserDeleteSuccessAction implements Action {
  public readonly  type = UserActionType.DeleteSuccess;
  constructor(public payload: UserResponse){}
 }

export class UserDeleteErrorAction implements Action{
  public readonly type = UserActionType.DeleteError;
  constructor(public error: any){}
}




export type UserAction = UserLoadAction
 | UserLogoutAction
 | UserLoadSuccessAction
 | UserLoadFailAction

 |UserInsertSuccessAction
 |UserInsertErrorAction
 |UserInsertLoadingAction
 |UserInsertAction

 |UserUpdateSuccessAction
 |UserUpdateErrorAction
 |UserUpdateLoadingAction
 |UserUpdateAction

 |UserDeleteSuccessAction
 |UserDeleteErrorAction
 |UserDeleteLoadingAction
 |UserDeleteAction
 |UserLoginAction;

