import { PropertyInput } from '../models/property.input';
import { PropertyLoad } from '../models/property.load';
import { Price, Area } from '../../../app.models';
import { environment } from 'src/environments/environment.prod';
import { User } from '../../register/models/user';

export function toFormData<T>( formValue: T ) {
    const formData = new FormData();

    for ( const key of Object.keys(formValue) ) {
      const value = formValue[key];
      formData.append(key, value);
    }
    console.log(formValue);
    return formData;
  }

export function img(url: string): string{
  if (url.startsWith('http')){
    return url;
  }else if (url){
      const arrayString: string[] = url.split('/');
      const imageName = arrayString[arrayString.length - 1];
      return `${environment.apiBaseUrl}/images/users/${imageName}`;
    }else{
      alert('');
      return '';
    }


  }

export function toUser(user: User, file: File): FormData{
    const data: FormData = toFormData(user);
    if (file){
      data.append('gallery', file);
    }

    return data;
  }


export function toProperty(pi: PropertyInput): FormData{
    const data: FormData = new FormData();
    const property: PropertyLoad = new PropertyLoad();
    property.id = 1;
    data.append('pgroup', pi.pgroup);
    data.append('_id', pi._id);
    data.append('id', new Date().toISOString());
    property.title = pi.basic.title;
    data.append('title', pi.basic.title);
    property.desc = pi.basic.desc;
    property.shortDesc = pi.basic.shortDesc;
    if (pi.basic.shortDesc){
      data.append('shortDesc', pi.basic.shortDesc );
    }else{
      data.append('shortDesc', '' );
    }

    data.append('desc', pi.basic.desc );
    property.propertyType = pi.basic.propertyType.name;
    data.append('propertyType',  pi.basic.propertyType.name);
    if (pi.media.advertise){
      data.append('advertise', pi.media.advertise?.toString());
    }


    if ( pi.basic.propertyStatus){
      pi.basic.propertyStatus.forEach(status => {
        data.append('propertyStatus', status.name);
      });
    }

    data.append('city', pi.address.city.name);
    data.append('zipCode', pi.address.zipCode);
    if ( pi.address.neighborhood){
    property.neighborhood = pi.address.neighborhood.map(x => x.name);
    }

    if (pi.address.neighborhood){
      pi.address.neighborhood.forEach(nb => {
        data.append('neighborhood' , nb.name);
      });
    }


    if (pi.address.street){
    pi.address.street.forEach(street => {
      data.append('street', street.name);
    });
  }

    if (pi.address.lat){
       data.append('lat', pi.address.lat.toString());
       data.append('lng', pi.address.lng.toString());
    }
    data.append('formattedAddress', pi.address.location);

    if ( pi.additional.features){
    pi.additional.features.forEach(feature => {
      data.append('features', feature.name);
    });
  }
    property.featured = pi.media.featured;
    data.append('featured', (pi.media.featured || false).toString());
    const price = new Price();
    price.rent = pi.basic.priceRent;
    price.sale = pi.basic .priceSale;
    property.priceDollar = price;
    property.priceEuro = price;
    data.append('rent', (pi.basic.priceDollar || 0).toString());
    data.append('sale', (pi.basic .priceEuro || 0).toString());
    property.bedrooms = pi.additional.bedrooms;
    data.append('bedrooms', (pi.additional.bedrooms || 0).toString());
    property.bathrooms = pi.additional.bathrooms;
    data.append('bathrooms', (pi.additional.bathrooms || 0).toString());
    property.garages = pi.additional.garages;

    data.append('garages', (pi.additional.garages || 0).toString() );

    data.append('area', (pi.additional.area || 0).toString());
    property.yearBuilt = pi.additional.yearBuilt;
    data.append('yearBuilt', (pi.additional.yearBuilt || 0).toString());
    property.ratingsCount = 0;
    property.ratingsValue = 0;
    property.additionalFeatures = pi.additional.features;

    if (pi.additional.features){
      pi.additional.features.forEach(feature => {
        data.append('features', feature.name);
      });
  }

    if (pi.media.additionalFeatures){
       pi.media.additionalFeatures.forEach(af => {
      data.append('additionalFeatures', af.name);
    });

    }

    property.videos = pi.media.videos;
    if (pi.media.videos){
    pi.media.videos.forEach(video => {
      data.append('videos', video.link);
    });
  }

    if (pi.basic.gallery){
      console.log(pi.basic.gallery);
      pi.basic.gallery.forEach(image => {

      if (image.file){
        data.append('gallery', image.file);
      }

      if (image.link){
        data.append('link', image.link);
      }

    });
  }
    property.published = new Date();
    property.lastUpdate = new Date();
    property.views = 0;

    return data;

}
