import { Action } from '@ngrx/store';
import { EmailParams } from '../models/email.params';
import { EmailResponse } from '../models/email.response';
import { Email } from '../models/email';



export enum EmailActionType {
  Loading = '[Email] Loading',
  LoadSuccess = '[Email] LoadSuccess',
  LoadFailure = '[Email] LoadFailure',

  InsertSuccess = '[Email] InsertSuccess',
  InsertLoading = '[Email] InsertLoading',
  InsertError = '[Email] InsertError',
  Insert = '[Email] InsertEmail',

  UpdateSuccess = '[Email] UpdateSuccess',
  UpdateLoading = '[Email] UpdateLoading',
  UpdateError = '[Email] UpdateError',
  Update = '[Email] UpdateEmail',

  DeleteSuccess = '[Email] DeleteSuccess',
  DeleteLoading = '[Email] DeleteLoading',
  DeleteError = '[Email] DeleteError',
  Delete = '[Email] DeleteEmail',

}

export class EmailLoadAction implements Action {
  public readonly type = EmailActionType.Loading;

  constructor(public payload: EmailParams) {
  }
}

export class EmailInsertAction implements Action {
  public readonly type = EmailActionType.Insert;
  constructor(public payload: EmailParams) {}
}

export class EmailInsertLoadingAction implements Action {
  public readonly type = EmailActionType.InsertLoading;
  constructor(public payload: EmailParams) {}
}

export class EmailInsertSuccessAction implements Action {
  public readonly  type = EmailActionType.InsertSuccess;
  constructor(public payload: EmailResponse){}
 }

export class EmailInsertErrorAction implements Action{
  public readonly type = EmailActionType.InsertError;
  constructor(public error: any){}
}

export class EmailLoadSuccessAction implements Action {
  public readonly type = EmailActionType.LoadSuccess;
  constructor(public payload: EmailResponse) {

  }
}


export class EmailLoadFailAction implements Action {
  public readonly type = EmailActionType.LoadFailure;
  constructor(public error: any) {
  }
}

// update
export class EmailUpdateAction implements Action {
  public readonly type = EmailActionType.Update;
  constructor(public payload: EmailParams) {}
}



export class EmailUpdateLoadingAction implements Action {
  public readonly type = EmailActionType.UpdateLoading;
  constructor(public payload: EmailParams) {}
}

export class EmailUpdateSuccessAction implements Action {
  public readonly  type = EmailActionType.UpdateSuccess;
  constructor(public payload: EmailResponse){}
 }

export class EmailUpdateErrorAction implements Action{
  public readonly type = EmailActionType.UpdateError;
  constructor(public error: any){}
}

// update
export class EmailDeleteAction implements Action {
  public readonly type = EmailActionType.Delete;
  constructor(public payload: Email) {}
}

export class EmailDeleteLoadingAction implements Action {
  public readonly type = EmailActionType.DeleteLoading;
  constructor(public payload: EmailParams) {}
}

export class EmailDeleteSuccessAction implements Action {
  public readonly  type = EmailActionType.DeleteSuccess;
  constructor(public payload: EmailResponse){}
 }

export class EmailDeleteErrorAction implements Action{
  public readonly type = EmailActionType.DeleteError;
  constructor(public error: any){}
}




export type EmailAction = EmailLoadAction
 | EmailLoadSuccessAction
 | EmailLoadFailAction

 |EmailInsertSuccessAction
 |EmailInsertErrorAction
 |EmailInsertLoadingAction
 |EmailInsertAction

 |EmailUpdateSuccessAction
 |EmailUpdateErrorAction
 |EmailUpdateLoadingAction
 |EmailUpdateAction

 |EmailDeleteSuccessAction
 |EmailDeleteErrorAction
 |EmailDeleteLoadingAction
 |EmailDeleteAction;
