import { Component, OnInit, Input } from '@angular/core';
import { Settings, AppSettings } from 'src/app/app.settings';
import { DomSanitizer } from '@angular/platform-browser';

import { GlobalState } from 'src/app/store/states/global.state';
import { Store } from '@ngrx/store';
import { selectAllProperty } from 'src/app/pages/properties/store/property.selectors';
import { pipe } from 'rxjs';
import { Property } from 'src/app/pages/properties/models/property';
import { selectAllPropertyDevelopment } from 'src/app/pages/properties/store/developments/property.developments.selectors';

@Component({
  selector: 'app-pictext',
  templateUrl: './pictext.component.html',
  styleUrls: ['./pictext.component.scss']
})
export class PictextComponent implements OnInit {
   public title = '';
   public description = '';
   @Input('position') position: number;
  @Input('property')prop: Property;
  public property: Property;

  public settings: Settings;
  public bgImage;
  public changeText: boolean;
  constructor(public appSettings: AppSettings,
              private store: Store<GlobalState>,
              ) {


  }

  public pictureClick(){

    this.changeText = true;
  }

  ngOnInit(): void {

    if (window.screen.width < 1000) { // 768px portrait
      // this.changeText = true;
    }

    this.store.select(pipe(selectAllPropertyDevelopment)).subscribe(properties => {
      console.log(properties);
      if (properties.length > 0){
      this.property = properties[this.position];
      this.loadProperty();
    }

    });

  }

  private loadProperty(){
    if (this.property && this.property.gallery && this.property.gallery[0]){
      this.title = this.property.title;
      this.description = this.property.desc;
      this.bgImage = this.property.gallery[0].medium ;
      //
    }
  }


}
