import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { PropertyParams } from '../models/property.params';
import { PropertyResponse } from '../models/property.response';
import { environment } from '../../../../environments/environment.prod';
import { Property } from '../../../app.models';
import { PropertyInput } from '../models/property.input';
import {toProperty} from '../utils/to-formdata';

@Injectable()
export class PropertyService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getPropertys(params: PropertyParams): Observable<PropertyResponse> {
    let page = '1';
    let limit = '10';
    let master = '';
    if (params.pageIndex){
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page){
       page = params.page.toString();
       limit = params.limit.toString();

    }
    if (params.pgroup){
     master = `&pgroup=${params.pgroup}`;
    }


    return this.httpClient.get<PropertyResponse>(`${this.url}/property?page=${page}&limit=${limit}${master}`, {});
  }

  public filterProperty(params: any): Observable<PropertyResponse>{
    return this.httpClient.post<PropertyResponse>(`${this.url}/property/filter`, (params));
  }

  public createProperty(params: PropertyInput): Observable<PropertyResponse>{
    const data: FormData = toProperty(params);
    return this.httpClient.post<PropertyResponse>(`${this.url}/property`, (data));
  }

  public updateProperty(params: PropertyInput): Observable<PropertyResponse> {
    const data: FormData = toProperty(params);
    return this.httpClient.put<PropertyResponse>(`${this.url}/property`, data);
  }

  public deleteProperty(params: Property): Observable<PropertyResponse>{
    return this.httpClient.delete<PropertyResponse>(`${this.url}/property?_id=${params._id}`);
  }

}

