
import { createSelector, createFeatureSelector } from '@ngrx/store';
import { paymentAdapter, PaymentState } from './payment.state';


export const {
  selectIds: _selectPaymentDataIds,
  selectEntities: _selectPaymentEntities,
  selectAll: _selectAllPayment,
  selectTotal: _selectPaymentTotal,
} = paymentAdapter.getSelectors();

export const selectPaymentState = createFeatureSelector<PaymentState>('payment');

export const selectPaymentIds = createSelector(
  selectPaymentState,
  _selectPaymentDataIds,
);

export const selectPaymentEntities = createSelector(
  selectPaymentState,
  _selectPaymentEntities,
);

export const selectAllPayment = createSelector(
  selectPaymentState,
  _selectAllPayment,
);

export const selectPaymentError = createSelector(
  selectPaymentState,
  (state: PaymentState): boolean => state.error,
);

export const selectPaymentLoading = createSelector(
  selectPaymentState,
  (state: PaymentState): boolean => state.loading,
);

export const selectPaymentTotal = createSelector(
  selectPaymentState,
  (state: PaymentState): number => state.total,
);
