import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { PgroupParams } from '../models/pgroup.params';
import { PgroupResponse } from '../models/pgroup.response';
import { Pgroup } from '../models/pgroup';
import { environment } from 'src/environments/environment.prod';


@Injectable()
export class PgroupService {

  constructor(private httpClient: HttpClient) { }
  private url = `${environment.apiBaseUrl}/api`;

  public getPgroups(params: PgroupParams): Observable<PgroupResponse> {
    let page = '1';
    let limit = '10';
    let _idString = '';
    if (params.pageIndex){
       page = (params.pageIndex + 1).toString();
       limit = params.pageSize.toString();
    }
    if (params.page){
       page = params.page.toString();
       limit = params.limit.toString();
    }
    if (params._id){
      _idString = `&_id=${params._id}`;
    }


    return this.httpClient.get<PgroupResponse>(`${this.url}/pgroup?page=${page}&limit=${limit}${_idString}`, {});
  }

  public createPgroup(params: PgroupParams): Observable<PgroupResponse>{
    return this.httpClient.post<PgroupResponse>(`${this.url}/pgroup`, (params.pgroup));
  }

  public updatePgroup(params: PgroupParams): Observable<PgroupResponse> {
    return this.httpClient.put<PgroupResponse>(`${this.url}/pgroup`, params.pgroup);
  }

  public deletePgroup(params: Pgroup): Observable<PgroupResponse>{
    return this.httpClient.delete<PgroupResponse>(`${this.url}/pgroup?_id=${params._id}`);
  }

}

