
import { createSelector, createFeatureSelector } from '@ngrx/store';
import { pgroupAdapter, PgroupState } from './pgroup.state';


export const {
  selectIds: _selectPgroupDataIds,
  selectEntities: _selectPgroupEntities,
  selectAll: _selectAllPgroup,
  selectTotal: _selectPgroupTotal,
} = pgroupAdapter.getSelectors();

export const selectPgroupState = createFeatureSelector<PgroupState>('pgroup');

export const selectPgroupIds = createSelector(
  selectPgroupState,
  _selectPgroupDataIds,
);

export const selectPgroupEntities = createSelector(
  selectPgroupState,
  _selectPgroupEntities,
);

export const selectAllPgroup = createSelector(
  selectPgroupState,
  _selectAllPgroup,
);

export const selectPgroupError = createSelector(
  selectPgroupState,
  (state: PgroupState): boolean => state.error,
);

export const selectPgroupLoading = createSelector(
  selectPgroupState,
  (state: PgroupState): boolean => state.loading,
);

export const selectPgroupTotal = createSelector(
  selectPgroupState,
  (state: PgroupState): number => state.total,
);
