import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Property } from 'src/app/app.models';




export interface PropertyDevelopmentState extends EntityState<Property> {
  error: boolean;
  loading: boolean;
  total: number;
  page: number;
}

export const propertyAdapter: EntityAdapter<Property> = createEntityAdapter<Property>({
  selectId: (property: Property) => property._id,
});

export const initialPropertyDevelopmentState: PropertyDevelopmentState = propertyAdapter.getInitialState({
  error: false,
  loading: true,
  total: 0,
  page: 0,
});
