import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Payment } from '../models/payment';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { Store, select } from '@ngrx/store';
import { PaymentLoadAction, PaymentDeleteAction, PaymentInsertAction, PaymentUpdateAction } from '../store/payment.actions';
import { PaymentService } from '../services/payment.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PaymentParams } from '../models/payment.params';
import { Hunit } from '../../../pages/account/housingunit/models/hunit';


import { selectAllPayment, selectPaymentTotal } from '../store/payment.selectors';
import { MatSnackBar } from '@angular/material/snack-bar';
import { HunitLoadAction } from 'src/app/pages/account/housingunit/store/hunit.actions';
import { AppService } from 'src/app/app.service';
import { GlobalState } from 'src/app/store/states/global.state';
import { img } from 'src/app/pages/properties/utils/to-formdata';


@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.scss'],
})
export class PaymentComponent implements OnInit {
  displayedColumns: string[] = [ 'paymentDate', 'paymentMethod', 'refNumber',  'amount', 'status', 'actions' ];
  dataSource: MatTableDataSource<Payment>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public payment: Payment = {};
  private hunitId: string;
  public hunit: Hunit = {};

  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private paymentService: PaymentService,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.store.pipe(select(selectAllPayment)).subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });


    this.store.pipe(select(selectPaymentTotal)).subscribe(total => {
        this.total = total;
       });


    this.activatedRoute.params.subscribe(params => {
      this.hunitId = params.id;
      this.loadPayment({page: 1, limit: 10, hunitId: params.id});
      this.store.dispatch(new HunitLoadAction({_id: params.id}));
    });

  }

 public openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
       duration: 2000,
    }).afterDismissed().subscribe(info => {
      if (info.dismissedByAction === true) {

      }
    });
 }

public loadPayment(data) {
   this.store.dispatch(new PaymentLoadAction(data));
  }

  public remove(payment: Payment) {
    this.snackBar.open('Are you sure you want to delete?', 'DELETE', {
      duration: 2000,
   }).afterDismissed().subscribe(info => {
     if (info.dismissedByAction === true) {
      this.store.dispatch(new PaymentDeleteAction(payment));
      const index: number = this.dataSource.data.indexOf(payment);
      if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Payment>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
     }});


  }

  public getColor(status) {
    switch (status) {
      case 'APPROVED':
        return 'green';
      case 'DECLINED':
        return 'red';
      case 'PENDING':
        return 'black';
    }
  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

public img(url: string) {
  if (url) {
    return img(url);
  }
  return '';

}


openEditDialog(payment: Payment): void {

  const dialogRef = this.dialog.open(AddPaymentComponent, {
    width: '500px',
    data: payment,
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');

    this.payment = result;
    this.payment._id = payment._id;
    this.payment.hunitId = this.hunitId;
    const paymentParams =  { payment: this.payment} as PaymentParams;
    this.store.dispatch(new PaymentUpdateAction(paymentParams));

  });


}

// tslint:disable-next-line: variable-name
public updatePayment(status: string, _id: string ) {
  const payment: Payment = {_id, status};
  const paymentParams =  { payment} as PaymentParams;
  this.store.dispatch(new PaymentUpdateAction(paymentParams));
}

openDialog(): void {
  const dialogRef = this.dialog.open(AddPaymentComponent, {
    width: '500px',
    data: this.payment,
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    if (result) {
      this.payment = result;
      this.payment.hunitId = this.hunitId;
      const paymentParams =  { payment: this.payment} as PaymentParams;
      this.store.dispatch(new PaymentInsertAction(paymentParams));
    }

  });
}

}

@Component({
  selector: 'app-add-payment',
  templateUrl: './add-payment.component.html',
  styleUrls: ['./add-payment.component.scss'],
})
export class AddPaymentComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<AddPaymentComponent>,
    public formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA)
    public data: Payment) {

    }
    public title = 'Add Payment';
    public paymentMethod = [
      { id: 1, name: 'MPESA' },
      { id: 2, name: 'CASH' },
      { id: 3, name: 'BANK' },
      { id: 4, name: 'CHEQUE' },
      { id: 5, name: 'VISA' },
      { id: 6, name: 'PAYPAL' },
    ];
    public type =  { id: 1, name: 'BANK' };
    public infoForm: FormGroup;

  public closeDialog() {
    this.dialogRef.close();
  }
  public onInfoFormSubmit(payment: Payment) {
    if (this.infoForm.valid) {
      this.data = payment;
      this.dialogRef.close();
    }

  }

  ngOnInit(): void {

    this.infoForm = this.formBuilder.group({
      paymentMethod: ['', Validators.required],
      refNumber: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      amount: [''],
      accountDetails: [''],
      paymentDate: [''],
      description: [''],
    });

    if (this.data && this.data._id) {
      this.title = 'Make  Payment for bill' +   this.data.refNumber;

    }

  }

}

